/**
 * 
 * v-link           - T.go()
 */
import Vue from "vue";

/**
 * v-linkT.go()
 * @value T.go()T.back()
 * @modifier back - T.backT.go
 * 
 * @example
 *      <v-button v-link="'/login'"></v-button>
 *      <a v-link.back></a>
 */
Vue.directive("link", (el, binding) => {
    el._uxLink = binding.value;
    el._uxLinkFn = binding.modifiers.back ? "back" : "go";
    if (binding.oldValue === undefined) { //bind
        el.addEventListener('click', () => {
            T[el._uxLinkFn](el._uxLink);
        });
    }
});


