import './common/css/base'; //css
import 'babel-polyfill'; //es6API
import Vue from 'vue';
import './common/js'; //js
import './core'; //
import './components/base'; //
import PageBase from '@js/PageBase'; //
import {
    tPage,
    tHeader,
    tFlex,
    tGroup,
    tCell,
    sLogo,
    tPopup,
    tInput,
    tButton
} from 'team-ui'

Vue.component('tPage', tPage)
Vue.component('tHeader', tHeader)
Vue.component('tFlex', tFlex)
Vue.component('tGroup', tGroup)
Vue.component('tCell', tCell)
Vue.component('sLogo', sLogo)
Vue.component('tPopup', tPopup)
Vue.component('tInput', tInput)
Vue.component('tButton', tButton)

T.vue = Vue;

FastClick.attach(document.body);

//Vue
if (process.env.silent) { // Vue (log)(warning)
    Vue.config.silent = true;
} else {
    Vue.config.productionTip = false; //
}
//
Vue.use(vueErrorHandler);
//test
if (process.env.isDesktop) {
    require('../test');
}
//mock
if (process.env.resourceEnv == 'mock') {
    require('../mock');
}
//<html>fontSizerem
let fontSize = (innerWidth * 100 / 750).toFixed(5);
document.getElementsByTagName('html')[0].style.fontSize = fontSize + 'px';

// H5 
// body
// document.body.classList.add(T.Device.os);
// iphoneX
// if (T.Device.phoneModel === 'iphoneX') {
//     document.body.classList.add('iphoneX');
// }

// & 
let isNativeReady = false,
    pageCmp;
// T.Native.onReady().then(params => { //
    // T.params = params;
    isNativeReady = true;
    if (pageCmp) { //init()
        init(pageCmp);
    }
    // @TODO
    if (T.Device.os == 'android') {
        T.Native.onBack();
    }
// });
/**
 * 
 * @param {Object} cmp - vue
 */
window.init = cmp => {
    if (isNativeReady) { //
        let Profile = Vue.extend(PageBase);
        return new Profile(cmp).$mount('#app');
    } else {
        pageCmp = cmp;
    }
}


