/**
 * Number.toFixed()bugvar a = 123456.555; a.toFixed(2); //"123456.55"
 */
Number.prototype.toFixed = function (precision) {
    let n = Math.round(this * Math.pow(10, precision)) / Math.pow(10, precision),
        ret = n.toString(),
        index = ret.lastIndexOf('.');
    if (precision > 0) { //0
        if (index == -1) {
            ret += '.'.padEnd(precision + 1, '0');
        } else {
            let diff = index - ret.length + precision + 1; //0
            if (diff > 0) {
                ret += ''.padEnd(diff, '0');
            }
        }
    }
    return ret;
}

//
//adnroidzwindow.onresizeioswindow.onresize
let isAndroid = navigator.userAgent.indexOf('iPhone') == -1;
if (isAndroid) {
    let body = document.querySelector('body'),
        clientH = innerHeight,
        keyboardUp = false,
        clientHB4KeyboardUp = clientH, //
        bodyScrollTop = 0; //
    window.onresize = function (e) {
        const h = innerHeight;
        if (clientH == h) return; //
        if (h < clientH) { //
            document.activeElement && document.activeElement.scrollIntoViewIfNeeded(); //
            bodyScrollTop = body.scrollTop;
            keyboardUp = true;
            clientHB4KeyboardUp = clientH;
        } else { //
            if (isAndroid) { //
                if (bodyScrollTop) { //bug
                    body.style.marginTop = '1px';
                    setTimeout(() => {
                        body.style.marginTop = 0;
                    }, 0);
                    bodyScrollTop = 0;
                } else {
                    body.scrollTop = 0; //scrollIntoViewIfNeeded()
                }
            }
            keyboardUp = false;
        }
    }
}
//iOSscrollTop
let isIphone = navigator.userAgent.indexOf('iPhone') > -1;
if( isIphone ){
    document.body.addEventListener('focusout', () => {
        let body = document.querySelector('body');
        //
        body.style.marginTop = '1px';
        setTimeout(() => {
            body.style.marginTop = 0;
        }, 0);
    })
}
//
document.body.addEventListener('touchstart', (e)=>{
    let activeDom = document.activeElement;
    if(activeDom && activeDom !== e.target) {
        let activeTagName = activeDom.tagName,
            targetTagName = e.target.tagName;
        if((activeTagName == 'INPUT' || targetTagName == 'TEXTAREA') 
                && (targetTagName != 'INPUT' && targetTagName != 'TEXTAREA')) {
            activeDom.blur();
        }
    }
}, false);


