/**
 * -T.Array
 *
 * each()       - 
 * last()       - 
 * removeOf()   - 
 */

/**
 * false
 * @param {Array} arr - 
 * @param {Function} fn - falseitem-index-arr-
 * @param {Boolean} [reversed=false] - 
 */
export function each(arr, fn, reversed) {
    let len = arr.length;
    if (reversed !== true) {
        for (let i = 0; i < len; i++) {
            if (fn(arr[i], i, arr) === false) break;
        }
    } else {
        for (let i = len - 1; i >= 0; i--) {
            if (fn(arr[i], i, arr) === false) break;
        }
    }
}
/**
 * 
 * @param {Array} arr - 
 * @return {*} 
 */
export function last(arr) {
    return arr[arr.length - 1];
}
/**
 * 
 * @param {*Array} arr - 
 * @param {*} item - 
 * @return {Number} -1
 */
export function removeOf(arr, item) {
    let i = -1;
    each(arr, (_item, _i) => {
        if (_item === item) {
            arr.splice(_i, 1);
            i = _i;
        }
    }, true);
    return i;
}
/**
 * 
 * @param {Array} arr - 
 * @return {Array} 
 */
export function uniq(arr){
    return [...new Set(arr)];
}


