<template>
  <div class="ui-switch" :class="{'ui-checked':value,'ui-disabled':disabled}" @touchstart="touchstart" @touchend="touchend">
    <i :class="{touching}"></i>
  </div>

</template>

<script>
  export default {
    name: "vSwitch",
    props: {
      value: Boolean, //
      disabled: Boolean //
    },
    data() {
      return {
        touching: false, // 
      };
    },
    methods: {
      touchstart() {
        this.touching = true;
      },
      touchend() {
        this.touching = false;
        this.$emit(
          "input",
          this.value ? false : true
        );

        this.$emit(
          "change",
          this.value ? false : true
        );

      }
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .ui-switch {
    position: relative;
    float: left;
    width: 3rem;
    height: 1.8rem;
    border-radius: 10rem;
    background-color: #ccc;
    transition: background-color 0.2s ease-in-out;
    i {
      position: absolute;
      top: 0.1rem;
      left: 0.1rem;
      width: 1.6rem;
      height: 1.6rem;
      border-radius: 10rem;
      background-color: #fff;
      box-shadow: 0 0.2rem 0.3rem rgba(0, 0, 0, 0.2);
      transition: 0.2s ease-in-out;
      transition-property: left, transform, width;
      &.touching {
        width: 2rem;
      }
    }
    &.ui-checked {
      background-color: $checked-switch;
      i {
        transform: translate3d(1.2rem, 0, 0);
        &.touching {
          left: -0.3rem;
        }
      }
    }
    &.ui-disabled,
    .ui-disabled & {
      opacity: 0.6;
      i {
        box-shadow: none;
      }
    }
    .common-li>&,
    .common-ul>li>& {
      float: right;
      margin: 0.7rem 1rem 0;
    }
  }
</style>


