<template>
  <div class="T_button t_vars" :class="className" @click="click" :style="style">
    <slot></slot>
  </div>
</template>
<script>
export default {
  data () {
    return {
      beclick: false
    }
  },
  props: {
    size: {
      type: String,
      default: 'normal' //normal small tiny
    },
    type: {
      type: String,
      default: 'primary' //primary
    },
    plain: {
      type: Boolean,
      default: false
    },
    disable: {
      type: Boolean,
      default: false
    },
    color: {
      type: Object,
      default: {}
    }
  },
  computed: {
    className () {
      let className = [
        `T_button_size_${this.size}`,
        `T_button_type_${this.type}${this.plain ? '_plain' : ''}`,
        this.beclick ? 'T_button_beclick' : '',
        `T_button_${this.disable ? 'disable' : 'able'}${this.plain ? '_plain' : ''}`
      ]
      return className
    },
    style () {
      let style = {
        backgroundColor: this.plain ? this.color.plain : this.color.main,
        color: this.plain ? this.color.main : this.color.plain,
        border: `1px solid ${this.color.main}`
      }
      return style
    }
  },
  methods: {
    click (e) {
      if (!this.beclick && !this.disable) {
        this.beclick = true
        this.$emit('click', e)
        setTimeout(() => {
          this.beclick = false
        }, 200)
      }
    }
  }
}
</script>
<style>
.T_button_size_normal {
  height: .88rem;
  width: 6.9rem;
  margin: auto;
  border-radius: .1rem;
  text-align: center;
  line-height: .88rem;
  font-size: 0.32rem;
}
.T_button_size_small {
  height: .8rem;
  padding: 0px .6rem;
  line-height: .8rem;
  text-align: center;
  display: inline-block;
  font-size: 0.32rem;
  border-radius: .1rem;
}
.T_button_size_tiny {
  height: .6rem;
  font-size: 0.24rem;
  padding: 0px .3rem;
  line-height: .6rem;
  text-align: center;
  display: inline-block;
  border-radius: .04rem;
  min-width: .6rem;
}
.T_button_type_primary {
  color: #fff;
  background-color: var(--color-cm);
  border: 1px solid var(--color-cm);
}
.T_button_type_primary_plain {
    color: var(--color-cm);
    background-color: #fff;
    border: 1px solid var(--color-cm);
}
.T_button.T_button_disable {
  background-color: #ccc;
  color: white;
  border: 1px solid #ccc;
}
.T_button.T_button_disable_plain {
    background-color: white;
    color: #ccc;
    border: 1px solid #ccc;
}
.T_button_beclick {
  opacity: .4;
}
</style>



