<!-- dialog -->
<!-- @author -->
<!-- @prop title [string]  -->
<!-- @prop msg [string]  -->
<!-- @prop img [string] require -->
<!-- @prop okButton [string] '' -->
<!-- @prop cancelButton [string] , -->
<!-- @prop extendBtns [Array]  -->
<!-- @return [Promise] truefalse -->
<template>
  <t-popup
    :value="value"
    @afterLeave="hide"
    :popup="{
      enter: 'animated fadeIn faster',
      leave: 'animated fadeOut faster',
      position: 'center'
    }"
  >
    <div class="T_dialog">
      <div class="T_dialog_pad">
        <div class="T_dialog_title" v-if="options.title">{{options.title}}</div>
        <div class="T_dialog_msg" v-if="options.msg">{{options.msg}}</div>
        <div class="T_dialog_msg" :style="options.msgHtml.color && ('color:' + options.msgHtml.color)" v-if="options.msgHtml" v-html="options.msgHtml.msg" @click="options.msgHtml.click"></div>
        <div class="T_dialog_img" v-if="options.img"><img :src="options.img"></div>
      </div>
      <div class="T_dialog_btns" @click="click">
        <div class="T_dialog_cancel" v-if="options.cancelButton">{{options.cancelButton}}</div>
        <div class="T_dialog_submit">{{options.okButton}}</div>
      </div>
      <div class="T_dialog_eb" @click="click">
        <div class="T_dialog_extendBtns" :style="item.color && ('color:' + item.color)" v-if="extend.length !== 0" :data-back="item.value" :key="index" v-for="(item, index) in extend">{{item.key}}</div>
      </div>
    </div>
  </t-popup>
</template>

<script>
export default {
  props: {
    value: Boolean,
    options: {
      default() {
        return {
          title: "",
          msg: "",
          msgHtml: {},
          zIndex: null,
          img: "",
          okButton: "",
          cancelButton: ""
        };
      }
    },
    extend: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  methods: {
    click(e) {
      this.$emit("click", e);
      this.value = false;
    },
    hide() {
      this.$emit("input", false);
      this.$emit("hide");
    }
  }
};
</script>
<style>
.T_dialog {
  background-color: white;
  border-radius: 5px;
  width: 5.9rem;
  margin: auto;
  padding-top: 0.4rem;
}
.T_dialog_pad {
  margin: 0px 0.3rem;
}
.T_dialog_img {
  margin-top: 0.18rem;
}
.T_dialog_img img {
  width: 100%;
  display: block;
}
.T_dialog_title {
  font-size: 0.36rem;
  color: #333;
  font-weight: bold;
  text-align: center;
  margin-top: 0.24rem;
}
.T_dialog_msg {
  font-size: 0.28rem;
  color: #666;
  text-align: center;
  padding: 0px 0.2rem;
  margin-top: 0.24rem;
}
.T_dialog_btns {
  border-top: 1px solid #ddd;
  margin-top: 0.64rem;
  position: relative;
  height: 0.96rem;
  line-height: 0.96rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
}
.T_dialog_btns > div {
  width: 100%;
  text-align: center;
  font-size: 0.28rem;
}
.T_dialog_cancel {
  color: #666;
  border-right: 1px solid #ddd;
}
.T_dialog_submit {
  color: #cc162c;
}
.T_dialog_extendBtns {
  border-top: 1px solid #ddd;
  height: 0.96rem;
  line-height: 0.96rem;
  text-align: center;
  font-size: 0.28rem;
  color: #666;
}
</style>



