<template>
  <t-popup 
  :value="value"
  @onHide="hide"
  >
    <div class="T_select">
			<t-header :leftOptions="[{icon: 'icon-icon_close'}]" v-if="title" @leftClick="hide">{{title}}</t-header>
			<div class="T_select_warp">
        <div class="T_select_list">
          <div class="T_select_group">
            <div class="T_select_cell" v-for="(item,index) in data" :title="item.key" :key="index + 'n'" @click="clickNormalItem(item)">
              <div class="T_select_left">
                <s-logo v-if="item.iconType" :logo="item.iconType" class="T_select_bankLogo" slot="icon"></s-logo>
                <div v-html="item.key"></div>
              </div>
              <div class="T_select_right">
                <i v-if="item.value === selected" class="iconfont icon-list_radio"></i>
              </div>
            </div>
            <div class="T_select_cell" @click="buttonClick" v-if="button">{{button}}</div>
          </div>
        </div>
        <slot></slot>
			</div>
		</div>
  </t-popup>
</template>
<script>
import tPopup from '../popup'
import tHeader from '../header'

export default {
  components: {
    tPopup,
    tHeader
  },
  data () {
    return {
      // selected: ''
    }
  },
  props: {
    value: Boolean,
    title: String,
    data: {
      type: Array,
      default () {
        return []
      }
    },
    selected: String,
    button: String
  },
  watch: {
    selected (e) {
      if (e) {
        this.$emit('change', e)
      }
    }
  },
  methods: {
    hide () {
      this.$emit('input', false)
    },
    clickNormalItem(item){
      if(item.value != this.selected){
        this.$emit('click', item)
        this.selected = item.value
      }
      this.hide()
    },
    buttonClick () {
      this.$emit('btnClick')
      this.hide()
    }
  }
}
</script>
<style>
.T_select_twoline {
  color: #222;
}
.T_select_twoline > div:first-child {
    font-size: 0.32rem;
}
.T_select_twoline > div:last-child {
    font-size: 0.26rem;
}
.T_select_list {
  background-color: #f6f6f6;
  padding-bottom: 1px;
}
.list-wrapper {
  position: relative !important;
  background-color: #f6f6f6;
}
.T_select_group {
  background-color: white;
}
.T_select {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
          flex-flow: column;
  min-height: 8rem;
  max-height: 10rem;
  overflow: auto;
}
.T_select_warp {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-flow: column;
            flex-flow: column;
}
.T_select_.icon-list_icon_path- {
  color: #c8c9cc;
  font-size: 0.32rem;
}
.T_select .icon-list_radio {
  color: #d81e06;
  font-size: 0.4rem;
}
.T_select_bankLogo {
  font-size: 0.48rem;
  margin-right: 0.16rem;
}
.T_select_cell {
  line-height: initial;
  position: relative;
  height: 1.2rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  margin-left: 0.3rem;
  padding-right: 0.3rem;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.T_select_left {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 0.32rem;
    color: #222;
}
.T_select_right {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
            align-items: center;
}
.T_select_cell:first-child::before {
  border-top: none !important;
}
.T_select_cell::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  top: 0px;
  border-top: 1px solid #e6e6e6;
  -webkit-transform: scaleY(0.5);
          transform: scaleY(0.5);
}
</style>




