import Vue from 'vue'
import {vAlert} from './message'
import {vPage,vPageLoading,vPageBubble} from './page'
import {vHeader,vBtn,vBtnText,vSearch} from './header'
import {vGroup} from './group'
import {vCell} from './cell'
import {vInput} from './input'
import {vButton} from './button'
import {vTabBar} from './tabBar'
import {vSwitch} from './switch'
import {vPopup} from './popup'
import {vRadio,vRadioGroup} from './radio'
import {vCheckbox,vCheckboxGroup} from './checkbox'
import {vBadge} from './badge'
import {vScroller} from './scroller'
import {vSymbol} from './symbol'
import {vToast} from './toast'
import './mask'
// import './messageBox'
// import './toast'


const cmps = {
    vAlert,
    vPage, // 
    vHeader, // 
    vBtn, //  
    vBtnText, // 
    vSearch,
    vCell, // 
    vGroup, // 
    vInput, // 
    vButton, // 
    vTabBar, 
    vSwitch,
    vPopup,
    vRadio,
    vRadioGroup,
    vCheckbox,
    vCheckboxGroup,
    vBadge,
    vScroller, // scroller
    vSymbol, //
    vToast   // toast
};
for (let k in cmps) {
    Vue.component(k, cmps[k]);
}


