<template>
  <button class="ui-button" :class="{'loading':loading}" :disabled="disabled" @click="onClick">
    <i v-if="icon" v-show="!loading" :class="icon"></i>
    <span v-if="$slots.default" v-show="!loading || !loadingText">
      <slot></slot>
    </span>
    <span v-if="loadingText" v-show="loading">
      {{loadingText}}
    </span>
    <i v-if="loading" class="fa fa-circle-o-notch fa-spin"></i>
    <i v-if="iconR" :class="iconR"></i>
  </button>
</template>

<script>
  export default {
    name: 'vButton',
    props: {
      disabled: Boolean, //
      icon: String, //icon-
      iconR: String, //icon-
      loading: Boolean, //
      loadingText: String //
    },
    methods: {
      onClick(e) {
        this.$emit('click', e);
      }
    }
  }
</script>

<style lang="scss">

  .ui-button {
    display: block;
    margin: 1rem auto;
    padding: 0.7em 1em;
    width: 100%;
    line-height: 1;
    text-align: center;
    border-radius: $border-radius2;
    @extend %no-break;
    @include button-color($btn-bg);
    &.red {
      @include button-color($btn-red);
    }
    &.blue {
      @include button-color($btn-blue);
    }
    &.gray {
      @include button-color($btn-gray);
    }
    &.wide {
      letter-spacing: 0.2rem;
      text-indent: 0.2rem;
    }
    &.inline {
      display: inline-block;
      width: auto;
      margin: inherit;
    }
    &[disabled] {
      background-color: $bg-disabled;
    }
    &.loading {
      pointer-events: none;
      opacity: 0.7;
      .loading-icon {
        position: relative;
        top: 0.05rem;
        display: inline-block;
        animation: rotating 2s linear infinite;
        @keyframes rotating {
          0% {
            transform: rotate(0deg);
          }
          to {
            transform: rotate(1turn);
          }
        }
      }
    }
  }
</style>


