<template>
    <div class="ui-checkbox-group" :class="{'ui-disabled':disabled}">
        <slot></slot>
    </div>
</template>

<script>
export default {
    name: 'vCheckboxGroup',
    props: {
        value: Array,    //
        disabled: Boolean //
    },
    methods: {
        checkAll() { //
            let value = [];
            this.getAllValue(this, value);
            this.$emit('input', value);
        },
        getAllValue(vm, value) { //checkbox
            vm.$children.forEach((item) => {
                if (item._isVue) {
                    if (item.$options.name == 'vCheckbox') {
                        value.push(item.label);
                    }
                    if (item.$children) {
                        this.getAllValue(item, value);
                    }
                }
            });
        }
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">

</style>


