<template>
  <transition
    enter-active-class="animated fadeIn faster"
    leave-active-class="animated fadeOut faster"
    @after-enter="entry"
    @after-leave="close"
    >
    <div class="T_toast_bg" v-show="toastSwitch">
      <div class="T_toast_body">
        <div class="T_toast_icon" v-if="icon"><i class="iconfont" :class="icon" :style="'color:' + iconColor"></i></div>
        <div class="T_toast_image" v-if="!icon && image"><img :src="image"></div>
        <div class="T_toast_msg" v-if="msg">{{msg}}</div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  data () {
    return {
      toastSwitch: false
    }
  },
  props: {
    msg: String,
    icon: String,
    iconColor: {
      type: String,
      default: 'white'
    },
    image: String,
    value: false,
    duration: Number
  },
  watch: {
    value: {
      handler (e) {
        setTimeout(() => {
          this.toastSwitch = e
        },10)
      },
      immediate: true
    }
  },
  methods: {
    close () {
      this.$emit('input', false)
      this.$emit('close')
    },
    entry () {
      if (this.duration) {
        setTimeout(() => {
          this.close()
        },this.duration)
      }
    }
  }
}
</script>
<style>
.T_toast_bg {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.T_toast_body {
  padding: .4rem;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 8px;
}
.T_toast_image {
  line-height: initial;
  width: .8rem;
  height: .8rem;
  text-align: center;
  margin: auto;
}
.T_toast_image > img {
  width: 100%;
  height: 100%;
  display: block;
}
.T_toast_image > i {
  font-size: .8rem;
}
.T_toast_icon + .T_toast_msg {
  margin-top: .24rem;
}
.T_toast_msg {
  text-align: center;
  font-size: .32rem;
  color: white;
}
</style>



