<template>
  <div class="page">
    <!---->
    <div class="header" v-if = "!runInNative">
      <slot name="header"></slot>
    </div>
    <!---->
    <div class="container" :style="'background-color:'+options.bgColor">
      <div ref="wrapper" class="wrapper">
        <div>
          <slot></slot>
          <div class="pullup-wrapper" :style="pullUpStyle" v-if="options.scroll && options.scroll.pullUpRefresh">
            <!-- <p-loading></p-loading> -->
          </div>
        </div>
      </div>
      <div class="pulldown-wrapper" :style="pullDownStyle" v-if="options.scroll &&options.scroll.pullDownRefresh">
        <div class="before-trigger" v-if="beforePullDown">
          <v-page-bubble :y="bubbleY"></v-page-bubble>
        </div>
        <div class="after-trigger" v-else>
          <div class="scroll-loading">
            <v-page-loading></v-page-loading>
          </div>
        </div>
      </div>
    </div>
    <!---->
    <div class="footer">
      <slot name="footer"></slot>
    </div>
  </div>
</template>
<script>
  import BScroll from "better-scroll";
  export default {
    props: {
      options: {
        type: Object,
        default: function () {
          return {
            scroll: false
          };
        }
      }
    },
    name: "vPage",
    data() {
      return {
        bubbleY: 0,
        pullDownStyle: "top: -50px",
        pullUpStyle: "bottom: -20px",
        beforePullDown: true,
        pullDownInitTop: -50,
        headerOnTop: true,
        runInNative: true, //
      };
    },
    methods: {
      createScroll: function () {
        let bScroll = new BScroll(this.$refs.wrapper, {
          click: true,
          momentum: true,
          pullDownRefresh: this.options.scroll.pullDownRefresh,
          pullUpLoad: this.options.scroll.pullUpRefresh,
          scrollbar: false
        });
        bScroll.on(
          "scroll",
          pos => {
            if (this.beforePullDown) {
              this.bubbleY = Math.max(0, pos.y + this.pullDownInitTop);
              this.pullDownStyle =
                `top:${Math.min(
                pos.y + this.pullDownInitTop,
                0
              )}px`;
            } else {
              this.bubbleY = 0;
            }
          },
          this
        );
        bScroll.on(
          "pullingDown",
          function () {
            this.beforePullDown = false;
            this.options.scroll.pullDownRefresh.call({},
              function () {
                this.pullDownStyle = `top:${this.pullDownInitTop}px`;
                this.beforePullDown = true;
                bScroll.refresh();
                bScroll.finishPullDown();
              }.bind(this)
            );
          }.bind(this)
        );
        bScroll.on(
          "pullingUp",
          function () {
            this.options.scroll.pullUpRefresh.call({}, function () {
              bScroll.refresh();
              bScroll.finishPullUp();
            });
          }.bind(this)
        );
      }
    },
    mounted: function () {
      if (this.options.scroll) {
        this.createScroll();
      }
    }
  };
</script>
<style lang='scss'>
  .page {
    position: absolute;
    width: 100%;
    height: 100%;
    @include flex(column);

    .container {
      position: relative;
      @include fill;
      background-color: $bg;
      height: 1px; //
      overflow-x: hidden;
      overflow-y: auto;
      margin-top: -2px;
      overflow-x: hidden;
    }

    .footer {
      @include fix;
    }

    .wrapper {
      position: absolute;
      width: 100%;
      left: 0px;
      top: 0px;
      height: 100%;
      z-index: 2; //overflow: hidden; 
      //overflow: auto;
      >div {
        min-height: calc(100% + 1px);
        position: relative;
      }
    }

    .pulldown-wrapper {
      z-index: 1;
      position: absolute;
      width: 100%;
      left: 0;
      @include flex(row, center, center);
      transition: all;
      .after-trigger {
        margin: 0.5rem 0;
      }
    }

    .pullup-wrapper {
      z-index: 1;
      position: absolute;
      width: 100%;
      left: 0;
      transition: all;
      @include flex(row, center, center);
    }

    .header.absolute {
      position: absolute;
      width: 100%;
      top: 0px;
      left: 0px;
      z-index: 3;
    }

    .opacity {
      background-color: transparent;
      >div:nth-child(2) {
        display: none;
      }
    }

  }
</style>


