<template>
  <t-cell>
    <div slot="left" class="t_cellInput_title" v-if="title">{{title}}</div>
    <t-input class="t_cellInput_input" v-model="value" :placeholder="placeholder" :disable="disable" :maxlength="maxlength" :readonly="readonly" :type="type" :textAlign="textAlign" :keyBoardType="keyBoardType"></t-input>
    <div slot="extend">
      <slot></slot>
    </div>
  </t-cell>
</template>
<script>
import tInput from '../input'
import tCell from '../cell'
export default {
  components: {
    tInput,
    tCell
  },
  props: {
    value: false,
    placeholder: String,
    maxlength: String,
    readonly: Boolean,
    type: String,
    textAlign: String,
    keyBoardType: String,
    title: String,
    disable: Boolean,
  },
  watch: {
    value (e) {
      this.$emit('input', e)
    }
  }
}
</script>
<style>
.t_cellInput_title{
  width: 1.68rem;
}
.t_cellInput_input{
  width: 100%;
  height: 100%;
  min-height: 1.04rem;
}
</style>



