/**
 * 
 */
const slide = {
    data({
        gestureLevel, //src/components/index.js
        dir, //x/y
        checkAngle = true, //
        maxPos = 999999, //
        minPos = -999999, //
        outsideTimes = 0 //
    }) {
        if (!Array.isArray(outsideTimes)) {
            outsideTimes = [outsideTimes, outsideTimes];
        }
        return {
            gestureLevel, //
            dirName: dir == "x" ? "pageX" : "pageY", //x/y
            forbid: false, //
            touching: false, //
            pos: 0, //
            maxPos, //
            minPos, //
            checkAngle, //
            outsideTimes, //
            points: {
                //
                pageX: 0,
                pageY: 0
            },
            flick: 0, //
            flickTime: 0 //
        };
    },
    touchstart(e) {
        //
        let activeDom = document.activeElement;
        if (activeDom && (activeDom.tagName == 'INPUT' || activeDom.tagName == 'TEXTAREA')) {
            this.forbid = true;
            return;
        }
        this.points.pageX = e.touches[0].pageX;
        this.points.pageY = e.touches[0].pageY;
        this.flick = this.points[this.dirName];
        this.flickTime = e.timeStamp;
        this.forbid = false;
    },
    touchmove(e) {
        if (this.forbid) return false;
        let diff = e.touches[0][this.dirName] - this.points[this.dirName];
        if (!this.touching) {
            if (this.checkAngle) {
                const otherDir = this.dirName == "pageX" ? "pageY" : "pageX",
                    otherDiff = e.touches[0][otherDir] - this.points[otherDir];
                if (Math.abs(diff) < Math.abs(otherDiff)) {
                    //<
                    this.forbid = true;
                    return;
                }
            }
            // @TODO
            // this.$store.commit("gestureIf", this);
            // if (this._uid != this.$store.state.gesture.id) {
            //     //
            //     this.forbid = true;
            //     return;
            // }
            this.touchMoveOnce && this.touchMoveOnce();
            this.touching = true;
        }
        e.preventDefault();
        let pos = this.pos + diff;
        if (pos > this.maxPos && diff > 0) {
            pos = this.outsideTimes[0] == 0 ? this.maxPos : (pos - diff * (1 - this.outsideTimes[0]));
        } else if (pos < this.minPos && diff < 0) {
            pos = this.outsideTimes[1] == 0 ? this.minPos : (pos - diff * (1 - this.outsideTimes[1]));
        }
        this.pos = pos;
        this.points[this.dirName] += diff;
        if (e.timeStamp - this.flickTime > 300) {
            //
            this.flick = this.points[this.dirName];
            this.flickTime = e.timeStamp;
        }
    },
    touchend(e) {
        if (!this.touching) return false;
        let speed = 0, //
            point = e.changedTouches[0][this.dirName]; //
        if (e.timeStamp > this.flickTime) {
            speed = (point - this.flick) / (e.timeStamp - this.flickTime);
        }
        // this.$store.commit("gesture"); //
        this.touching = false;
        return {
            speed,
            point
        };
    }
};
export default slide;


