<template>
  <div class="T_switch" :class="className" @click="click">
    <div class="T_switch_btn"></div>
  </div>
</template>
<script>
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    className() {
      return this.value ? "T_switch_open" : "T_switch_close";
    }
  },
  methods: {
    click() {
      this.$emit("input", !this.value);
    }
  }
};
</script>

<style>
.T_switch {
  width: 1rem;
  height: 0.5rem;
  border-radius: 0.5rem;
  -webkit-transition: all 0.35s cubic-bezier(0.45, 1, 0.4, 1) 0s;
  transition: all 0.35s cubic-bezier(0.45, 1, 0.4, 1) 0s;
  position: relative;
}
.T_switch_close {
  background-color: #eee;
}
.T_switch_close > .T_switch_btn {
  left: 0.05rem;
}
.T_switch_open {
  background-color: #cc162c;
}
.T_switch_open > .T_switch_btn {
  left: calc(100% - 0.05rem - 0.4rem);
}
.T_switch > .T_switch_btn {
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 100%;
  background-color: white;
  -webkit-transition: all 0.35s cubic-bezier(0.45, 1, 0.4, 1) 0s;
  transition: all 0.35s cubic-bezier(0.45, 1, 0.4, 1) 0s;
  position: absolute;
  top: 0.05rem;
}
</style>



