<template>
  <span v-text="text" :class="['ui-badge', {'ui-badge-dot': typeof text === 'undefined', 'ui-badge-single': typeof text !== 'undefined' && text.toString().length === 1}]"></span>
</template>

<script>
  export default {
    name: 'badge',
    props: {
      text: [String, Number]
    }
  }
</script>

<style lang="scss">
  .ui-badge {
    display: inline-block;
    text-align: center;
    background:$bg-orange;
    color: #fff;
    font-size: 0.728rem;
    height: 1rem;
    line-height: 1rem;
    border-radius: 0.5rem;
    padding: 0 0.375rem;
    background-clip: padding-box;
    vertical-align: middle;
  }

  .ui-badge-single {
    padding: 0;
    width: 1rem;
  }

  .ui-badge-dot {
    height: auto;
    padding: 0.315rem
  }
</style>


