<template>
  <div class="T_tabSelect">
    <div class="T_tabSelect_warp" :style="`left: ${scrollX}px`">
      <div @click="click(item)" v-for="(item, index) in tabs" :key="index" :ref="value === item.value ? 'choiced': ''" class="T_tabSelect_item" :class="{'T_tabSelect_choiced': value === item.value}">{{item.key}}</div>
      <div class="T_tabSelect_bar" :style="width + left"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      left: "",
      width: "",
      scrollX: 0
    };
  },
  props: {
    tabs: Array,
    value: String
  },
  watch: {
    value: {
      handler() {
        this.$nextTick(() => {
          let el = this.$refs.choiced[0];
          this.width = `width: ${el.clientWidth}px;`;
          this.left = `left: ${el.offsetLeft}px;`;
          let warpWidth = el.parentElement.clientWidth;
          let windowWidth = el.parentElement.parentElement.clientWidth;
          let scrollX = -(el.offsetLeft + el.clientWidth / 2) + windowWidth / 2;
          this.scrollX =
            scrollX > 0
              ? 0
              : scrollX < -warpWidth + windowWidth
                ? -warpWidth + windowWidth
                : scrollX;
        });
      },
      immediate: true
    }
  },
  methods: {
    click(e) {
      this.$emit("input", e.value);
    }
  }
};
</script>
<style>
.T_tabSelect {
  position: relative;
  height: 0.88rem;
  overflow: hidden;
  width: 100%;
}
.T_tabSelect_warp {
  height: 0.88rem;
  white-space: nowrap;
  display: -webkit-box;
  position: absolute;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  min-width: 100%;
}
.T_tabSelect_item {
  margin: 0px 0.4rem;
  color: #999;
  font-size: 0.36rem;
  height: 0.88rem;
  line-height: 0.88rem;
}
.T_tabSelect_choiced {
  color: #333;
}
.T_tabSelect_bar {
  position: absolute;
  height: 0.04rem;
  background-color: #cc162c;
  bottom: 0px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
</style>



