/**
 * js
 * 
 * @desc build
 */
window.onerror = function (text, url, line, column, err) {
    if (err) {
        T.Tracker.onerror(_resolveErr(err));
    }
    if (process.env.silent !== false) { //
        return true;
    }
}
/**
 * vue
 * Vue.use(vueErrorHandler);
 */
window.vueErrorHandler = function (vue) {
    var prev = vue.config.errorHandler;
    vue.config.errorHandler = handler;

    function handler(err, vm, info) {
        T.Tracker.onerror(_resolveErr(err, info));
        if (process.env.silent === false) { //
            console.error(err);
        }
        if (typeof prev === 'function') prev.call(this, err, vm, info); //
    }
}
//
function _resolveErr(err, vueInfo) {
    var name = err.name,
        message = err.message,
        stack = err.stack;
    if (vueInfo) {
        message = 'Error in ' + vueInfo + ': ' + message;
    }
    stack = stack.split(/\n\s+/).slice(1); //
    return {
        name: name, //'ReferenceError'
        message: message, //'a is not defined'
        stack: stack //['at Object.<anonymous> (http://localhost:9005/dist/static/js/demo/home/index.js:1:35148)', ...]
    };
}


