<template>
    <svg class="ui-symbol" aria-hidden="true">
        <use v-bind:xlink:href="'#icon-'+ name"></use>
    </svg>
</template>

<script>
export default {
    name: 'vSymbol',
    props: {
        //
        name: {
            type: String,
            required: true
        }
    }
}
</script>

<style lang="scss">
.ui-symbol {
    width: $font-size;
    height: $font-size;
    fill: currentColor;
    overflow: hidden;
}
</style>


