/**
 * -T.Object
 *
 * isEmpty          - null
 * isPlainObject    - 
 * getDeep          - 
 */

/**
 * null
 * @param {Object} v - 
 * @return {Boolean} nulltruefalse
 */
export function isEmpty(v) {
    if (typeof v == 'object') {
        for (let k in v) return false;
        return true;
    }
    return false;
}
/**
 * 
 * @param {*} v - 
 * @return {Boolean} 
 * @example isPlainObject({k: 1}); //true
 *          isPlainObject({k: {k2: 1}}); //true
 *          isPlainObject([]); //false
 *          isPlainObject(document); //false
 */
export function isPlainObject(v) {
    if (v && v.toString() == '[object Object]') {
        return true;
    }
    return false;
}
/**
 * 
 * @param {Object} obj - 
 * @param {...String} keys - 
 * @return {*}   undefined
 * @example 
 * if(getDeep(o, 'k1', 'k2')){ ... }  if(o && o.k1 && o.k1.k2){...}
 */
export function getDeep(obj, ...keys) {
    let v = obj;
    if (!v) return v;
    for (let i = 0; i < keys.length; i++) {
        v = v[keys[i]];
        if (!v) return v;
    }
    return v;
}


