/**
 * T.message()
 */

/**
 * Vue$message
 * @param {String} key - 
 * @return {String} 
 */
export function message(key) {
    switch (key) {
        case "sessionOut": //
            return "";
        case "timeout": //
            return "";
        case 'httpBizError': //
            return '';
    }
    return key;
};


