<template>
  <header :class="css">
    <slot></slot>
  </header>
</template>

<script>
  export default {
    name: "vHeader",
    props: {
      css: ""
    }
  };
</script>

<style lang="scss" scoped>
  /* @*/
  header {
    display: flex;
    justify-content: space-between;
    height: px2rem(88);
    background-color: #fff;
    color: #333;
    line-height: px2rem(88);
    position: relative;
    h1 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
      flex: 1;
      padding: 0 0;
      font-size: px2rem(30);
      text-align: center;
      @extend %no-break;
    }
    .text-left {
      text-align: left !important;
    }
    .btn {
      margin: 0;
      padding: 0 0;
      width: auto;
      min-width: px2rem(80);
      text-align: center;
    }
    .btn-icon {
      font-size: px2rem(40);
      i {
        line-height: px2rem(30);
      }
    }
    .btn-text {
      font-size: px2rem(30);
      padding: 0 0.5rem;
      i {
        font-size: px2rem(30);
      }
    }
  }

  header.white {
    background-color: #fff !important;
    color: $font-red;
  }

  header.black {
    background-color: #333 !important;
    color: #fff;
  }
</style>


