<template>
  <transition name="ui-mask">
    <div v-show='visible' class="ui-mask" @click='click'></div>
  </transition>
</template>
<script>
  export default {
    name: "v-mask",
    props: {
      visible: { type: Boolean },  // 
      maskClick: false             // 
    },
    data() {
      return {}
    },
    methods: {
      click() {
        if (!this.maskClick) return
        this.$emit("click")
      }
    }
  }
</script>
<style lang="scss">
  .ui-mask-enter,
  .ui-mask-leave-active {
    opacity: 0;
  }

  .ui-mask-leave-active,
  .ui-mask-enter-active {
    transition: opacity 300ms;
  }

  .ui-mask {
    position: fixed;
    z-index: 9999;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .6);
  }
</style>


