<template>
	<svg class="icon" aria-hidden="true">
			<use :xlink:href="`#icon-${logo}`"></use>
	</svg>
</template>
<style lang="scss" scoped>
.icon {
  /*  font-size  */
  width: 1em; height: 1em;
  /*  */
  vertical-align: -0.1em;
  /*  color  SVG /fill */
  fill: currentColor;
  /* path  stroke  viewBox  IE 
      normalize.css  */
  overflow: hidden;
}
</style>

<script>
	export default {
		name: 'symbolIcon',
		props: {
			logo: String
		}
	}
</script>



