<template>
  <transition name="ui-popup">
    <div v-show="show" class="ui-popup-body">
      <v-mask :visible="show" :maskClick="maskClick" @click="closePopup"></v-mask>
      <transition name="ui-popup-animate-bottom">
        <div v-show="show" class="ui-popup-box">
          <slot></slot>
        </div>
      </transition>
    </div>
  </transition>
</template>
<script>
  export default {
    name: 'popup',
    data() {
      return {
        show: false
      }
    },
    props: {
      value: {
        default: false
      },
      maskClick: {
        default: true
      }
    },
    methods: {
      closePopup: function () {
        this.show = false
      }
    },
    // body @
    mounted () {
        document.body.appendChild(this.$el)
    },
    watch: {
      show(val) {
        this.$emit('input', val)
      },
      value: {
        handler: function (val) {
          this.show = val
        },
        immediate: true
      }
    }
  }
</script>
<style lang='scss'>
  @mixin absolute {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
  }

  .ui-popup-body {
    @include absolute;
    display: flex;
    flex-flow: column-reverse;
    transition-property: left;
    transition-duration: 300ms;
    /*leftbottom @*/
    >.ui-popup-box {
      background-color:$bg-gray;
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      transition-property: transform;
      transition-duration: 300ms;
      z-index: 10000;
    }
  }

  .ui-popup-enter,
  .ui-popup-leave-active {
    left: 0px;
  }

  .ui-popup-animate-bottom-enter,
  .ui-popup-animate-bottom-leave-active {
    transform: translate3d(0, 100%, 0);
  }
</style>


