export function distOCR(opts) {
    return new Promise((resolve, reject) => {
        TurboJSBridge.call('distOCR', opts, ret => {
            if (ret.errorcode === 0) {
                resolve(ret);
            } else { //
                ret.errormsg = `${ret.errorcode}:${ret.errormsg}`
                reject(ret);
            }
        });
    });
}
