/**
 * 
 */
export default {
    isPage: true,
    beforeCreate() {
        T.vm = this;
        Object.freeze(T.params); //
        //backed
        T.Native.onComeBack().then(params => T._emitHook(T.vm, 'backed', params));
    },
    backed(params) {
        if (!T.isEmpty(params)) {
            T.params = Object.assign({}, T.params, params);
            Object.freeze(T.params); //
        }
    }
}


