<template>
  <div class="ui-group">
    <div class="ui-group-title" v-if="title">{{title}}</div>
    <slot></slot>
  </div>
</template>
<script>
  export default {
    name: "group",
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data() {
      return {}
    }
  }
</script>
<style lang="scss">
  .ui-group {
    margin: 0 0 0.5rem 0;
    .ui-group-title {
      padding: 0.5rem 1rem;
      color: #666666;
    }
  }
</style>


