/**
 * -T.String
 */

/**
 * 
 * @param {String} str - 
 * @param {String} [char] - 
 * @param {String} [direction] - l-r-
 */
export function trim(str, char, direction) {
    char = char || '\\s';
    const regStr = direction == 'l' ? `^${char}+` : (direction == 'r' ? `${char}+$` : (`^${char}+|${char}+$`)),
        reg = new RegExp(regStr, 'g');
    return str.replace(reg, '');
}


