<template>
  <div class="T_header">
    <div class="T_header_left">
      <slot name="left">
        <i class="iconfont" @click="leftClick(index)" v-for="(item, index) in leftOptions" :key="index" :class="[item.icon]">{{item.icon ? '' : item.text}}</i>
      </slot>
    </div>
    <div class="T_header_middle" @click="click">
      <slot>
        <div>{{title}}</div>
      </slot>
    </div>
    <div class="T_header_right">
      <slot name="right">
        <i class="iconfont" @click="rightClick(index)" v-for="(item, index) in rightOptions" :key="index" :class="[item.icon]">{{item.icon ? '' : item.text}}</i>
      </slot>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    leftOptions: {
      type: Array,
      default() {
        return [
          {
            icon: "icon-icon_back"
          }
        ];
      }
    },
    rightOptions: Array,
    title: String
  },
  methods: {
    leftClick(e) {
      if (this.$listeners.leftClick) {
        this.$emit("leftClick", e);
      } else {
        this.$router.go(-1);
      }
    },
    rightClick(e) {
      if (this.$listeners.rightClick) {
        this.$emit("rightClick", e);
      }
    },
    click (e) {
      this.$emit('click', e)
    }
  }
};
</script>
<style>
.T_header {
  height: 0.88rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  background-color: white;
  color: #222;
  font-size: 0.34rem;
  z-index: 1;
  box-shadow: 0 1px 4px 0 rgba(51, 51, 51, 0.1);
}
.T_header > * {
  height: 100%;
}
.T_header i {
  height: 0.88rem;
  width: 0.88rem;
  text-align: center;
  line-height: 0.88rem;
  display: inline-block;
}
.T_header > *:nth-child(2) {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
}
.T_header_right, .T_header_left {
  min-width: 0.88rem;
}
.T_header_middle {
  text-align: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
}
</style>



