<template>
  <div class="ui-input">
    <div class="set">
      <i v-if="icon" :class="icon"></i>
      <span v-if="title" v-text='title' class="title"></span>
      <input :placeholder="placeholder" :type="type" @input="search" v-model="value">
      <div v-if="value" class="clear fa fa-plus-circle" @click="clear"></div>
      <slot></slot>
    </div>
  </div>
</template>
<script>
  export default {
    name: "vInput",
    props: {
      icon: {
        type: String,
        default: ''
      },
      title: {
        type: String,
        default: ''
      },
      type: {
        type: String,
        default: 'text'
      },
      placeholder: {
        type: String,
        default: ''
      }
    },
    data() {
      return { value: null };
    },
    computed: {},
    watch: {},
    methods: {
      clear() {
        this.value = null
        this.$emit("input", this.value)
      },
      search() {
        this.$emit("input", this.value)
      }
    }
  }
</script>
<style lang="scss">
  .ui-input {
    -webkit-box-flex: 1;
    padding: 0 0;
    background: none;
    height: 2.5rem;
    margin: 0rem 0;
    .set {
      display: -webkit-box !important;
      position: relative;
      -webkit-box-flex: 1;
      width: 100%;
      height: 100%;
      -webkit-appearance: none;
      background: none;
      text-decoration: none;
      display: block;
      // border-bottom: #eaeaea solid 1px; // background-color: #f9f9f9;
      // border-radius: $border-radius2;
    }
    i {
      display: -webkit-box !important;
      color: #333;
      padding: 0em 10px;
      -webkit-box-align: center;
      -webkit-box-pack: center;
    }
    span {
      // min-width: 4rem;
      display: -webkit-box !important;
      margin-right: t(46);
      color: #333;
      padding: 0rem;
      -webkit-box-align: center;
      -webkit-box-pack: left;
    }
    input {
      display: -webkit-box !important;
      -webkit-box-flex: 1;
      width: 100%;
      padding: 0 t(27);
      font-size: t(32);
      color: #333;
      border: 0px;
      background: none;
      height: 100%;
      border-radius: 0;
      text-align: left;
    }
    .clear {
      display: -webkit-box !important;
      color: #E5E8EB;
      padding: 0em 10px;
      -webkit-box-align: center;
      -webkit-box-pack: center;
      -webkit-transform: rotate(45deg);
    }
  }

  .search .search ::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #6D6D6D;
  }
</style>


