<template>
    <div class="ui-scroller" :class="{'out-wall':outWall,'ui-scroller-x':direction == 'x'}"
            :style="style"
            @touchstart="touchstart" @touchmove="touchmove" @touchend="touchend" @webkitTransitionEnd.self="transitionEnd">
        <div v-if="toRefresh" v-show="!loadingMore" class="top-pocket">{{refreshText}}</div>
        <slot></slot>
        <div ref="bottomPocket" v-if="toLoadMore" v-show="loadingMore || nomore" class="bottom-pocket">{{loadMoreText}}</div>
    </div>
</template>

<script>
import Slide from '../../slide';

export default {
    name: 'vScroller',
    props: {
        direction: { //x/y
            type: String,
            default: 'y'
        },
        toRefresh: { //outToptrue
            type: Boolean,
            default: false
        },
        toLoadMore: { //outBottomtrue
            type: Boolean,
            default: false
        },
        outTop: { //
            type: Boolean,
            default: false
        },
        outBottom: { //
            type: Boolean,
            default: true
        },
        data: [Array, Object], // watchdatamin
        isCheckInput: {        // touchmoveactiveElement INPUT\TEXTAREA
            type: Boolean,
            default: false
        }
    },
    data() {
        return Object.assign(Slide.data({
            min: 0,           //  - loading
            gestureLevel: 10, //
            dir: this.direction, //
            checkAngle: this.direction == 'x', //
            outsideTimes: 1 / 3 //
        }), {
                downEnoughDiff: T.rem2px(4), //
                upEnoughDiff: T.rem2px(8), //
                topPocketHeight: T.rem2px(4), //
                bottomPocketHeight: T.rem2px(3), //
                outWall: false, //
                refreshing: false, //
                loadingMore: false, //
                nomore: false, //false
                refreshOff: false, //
                maxPos: 0, //
                minToken: 0 //min
            });
    },
    computed: {
        style() { //
            return {
                transitionDuration: `${this._uxDuration}ms`,
                transform: 'translate3d(' + (this.direction == 'x' ? `${this.pos}px,0,0)` : `0,${this.pos}px,0)`)
            };
        },
//        min() { //
//            this.minToken;
//            let wh = this.direction == 'x' ? 'Width' : 'Height',
//                wrap = this.$el.parentNode['offset' + wh],
//                el = this.$el['offset' + wh];
//            this.$el.style['min' + wh] = wrap + 'px';
//            return Math.min(0, wrap - el - (this.loadingMore || this.nomore ? this.bottomPocketHeight : 0));
//        },
        downEnough() { //
            return this.pos >= this.downEnoughDiff;
        },
        upEnough() { //
            return this.pos <= this.min + this.upEnoughDiff;
        },
        refreshText() { //
            if (this.refreshing) return '...';
            if (this.refreshOff) return '';
            if (this.downEnough) return '';
            return '';
        },
        loadMoreText() { //
            if (this.loadingMore) return '...';
            if (this.nomore) return '';
            return '';
        }
    },
    methods: {
        touchstart(e) {
            if (this.refreshOff) { //
                this.forbid = true;
                return;
            }
            if (this.inTransition) { //
                let tmp = this.pos;
                setTimeout(() => {
                    if (tmp == this.pos) { //60ms
                        this.scrollTo(this.getComputedTransform(), 0);
                        this.inTransition = false;
                    }
                }, 60);
            }
            Slide.touchstart.call(this, e);
            this.outWall = false;
            this.minPos = this.min;
        },
        touchmove(e) {
            this._isCheckInput()
            if (Slide.touchmove.call(this, e) === false) return;
            this.scrollTo(this.pos, 0);
            if (this.toLoadMore && !this.nomore && !this.loadingMore && this.upEnough && !this.refreshing) { //
                this.loadingMore = true;
                this.$emit('loadmore'); //loadmore
            }
        },
        touchend(e) {
            const ret = Slide.touchend.call(this, e);
            if (ret) {
                if (this.checkWall() === false) { //
                    const anim = this.momentum(ret.speed); //
                    this.scrollTo(anim.destination, anim.duration);
                }
                this.inTransition = true;
            }
        },
        transitionEnd() {
            this._uxDuration = 0;
            this.inTransition = false;
            if (this._uxNotEmitLoadMore) {
                this._uxNotEmitLoadMore = false;
                return;
            }
            if (this.refreshOff) { //
                if (this.pos <= 0) {
                    this.refreshOff = false;
                    this.outWall = false;
                }
            } else if (this.outWall) { //
                this.outWall = false;
            } else if (this.checkWall() === false) { // false-
            }
        },
        checkWall() { //
            let pos = this.pos;
            if (this.refreshing) {
                if (this.downEnough) {
                    this.outWall = true;
                    this.pos = this.downEnoughDiff;
                    return;
                }
            }
            if (this.refreshOff) return;
            if (!this.refreshing && !this.loadingMore) {
                if (this.toRefresh && this.downEnough) { //
                    this.refreshing = true;
                    this.outWall = true;
                    this.pos = this.downEnoughDiff;
                    this.$emit('refresh'); //refresh
                    return;
                }
                if (this.toLoadMore && !this.nomore && this.upEnough) { //
                    this.loadingMore = true;
                    this.$emit('loadmore'); //loadmore
                    this.outWall = true;
                    if (this.pos < this.min) {
                        this.pos = this.min;
                    }
                    return;
                }
            }
            if (pos > 0) {
                pos = 0;
            } else if (pos < this.min) {
                pos = this.min;
            } else {
                return false;
            }
            this.inTransition = true;
            this.outWall = true;
            this.pos = pos;
        },
        /**
         * 
         * @param {Boolean} [emitLoadMore=true] - loadmore
         */
        update(emitLoadMore = true) {
            this.$nextTick(() => {
                this.computeMin()
                this.minToken++;
                if (this.pos < this.min) { //
                    if (emitLoadMore === false) {
                        this._uxNotEmitLoadMore = true;
                    }
                    this.scrollTo(this.min);
                }
            });
        },
        /**
         * 
         * @param {Boolean} nomore - 
         */
        setNoMore(nomore) {
            this.nomore = nomore;
        },
        /**
         * 
         * @param {object} opts - 
         * @param {Boolean} [opts.nomore=false] - 
         */
        end({ nomore = false } = {}) {
            if (this.refreshing) { //
                this.refreshing = false;
                this.refreshOff = true;
                setTimeout(() => { //
                    if (this.pos > 0) {
                        this.refreshOff = true;
                        this.outWall = true;
                        this.pos = 0;
                        this.forbid = true;
                    } else {
                        this.refreshOff = false;
                    }
                }, 500);
            } else if (this.loadingMore) { //
                this.loadingMore = false;
            }
            this.nomore = nomore;
            this.update(false); //
        },
        /**
         * 
         * @param {Number} pos - y
         * @param {Number} [duration=100] - 
         */
        scrollTo(pos, duration = 100) {
            this._uxDuration = duration;
            this.pos = pos;
            this.$emit('scroll', pos);
        },
        momentum(speed) { //
            let deceleration = 0.0008, //
                duration = Math.abs(speed) / deceleration, //
                current = this.pos, //y
                destination = current + (speed * speed) / (2 * deceleration) * (speed < 0 ? -1 : 1); //y
            if (destination > 0) {
                if (!this._outTop) {
                    destination = 0;
                } else {
//                    destination = this.$tools.rem2px(2) + (this.refreshing ? this.topPocketHeight : 0);
                    destination = T.rem2px(2) + (this.refreshing ? this.topPocketHeight : 0);
                }
                let distance = Math.abs(current) + destination;
                duration = distance / Math.abs(speed);
            } else if (destination < this.min) {
                if (!this._outBottom) {
                    destination = this.min;
                } else {
//                    destination = this.min - this.$tools.rem2px(2) - (this.toLoadMore && !this.loadingMore ? this.bottomPocketHeight : 0);
                    destination = this.min - T.rem2px(2) - (this.toLoadMore && !this.loadingMore ? this.bottomPocketHeight : 0);
                }
                let distance = Math.abs(destination - current);
                duration = distance / Math.abs(speed);
            }
            return {
                destination: Math.round(destination),
                duration: duration
            };
        },
        /**
         * transformx/y
         * @return pos
         */
        getComputedTransform: function () {
            var styles = document.defaultView.getComputedStyle(this.$el, null),
                translateString = styles.getPropertyValue(this.$equipment.cssPre + 'transform') || styles[this.$equipment.cssPre + 'transform'],
                matches = translateString.match(/[\-\d\.]+/g);
            return Number(matches[this.direction == 'x' ? 4 : 5]);
        },
        // 
        computeMin () {
            let wh = this.direction == 'x' ? 'Width' : 'Height',
                wrap = this.$el.parentNode['offset' + wh],
                el = this.$el['offset' + wh];
            this.$el.style['min' + wh] = wrap + 'px';
            this.min = Math.min(0, wrap - el - (this.loadingMore || this.nomore ? this.bottomPocketHeight : 0));
        },
        // INPUT\TEXTAREA
        _isCheckInput () {
            !this.isCheckInput && (this.forbid = false)
        }
    },
    created() {
        this._outTop = this.toRefresh || this.outTop;
        this._outBottom = this.toLoadMore || this.outBottom;
        if (!this._outTop) {
            this.outsideTimes[0] = 0;
        }
        if (!this._outBottom) {
            this.outsideTimes[1] = 0;
        }
        this._uxDuration = 0; //
    },
    mounted () {
        this.$el.parentNode.classList.add('ui-scroller-wrap');
        setTimeout(() => {
            this.update();
        }, 0);
    },
    watch: {
        data () {
            this.$nextTick(() => {
                this.computeMin()
            })
        }
    }
}
</script>

<style lang="scss" rel="stylesheet/scss">
.ui-scroller {
    position: relative;
    z-index: 1;
    box-sizing: border-box;
    background-color: $bg-gray;
    transition: transform 0ms cubic-bezier(0.1, 0.57, 0.1, 1);
    .top-pocket {
        position: absolute;
        left: 0;
        bottom: 100%;
        width: 100%;
        line-height: 4;
        text-align: center;
        color: $font-gray;
    }
    .bottom-pocket {
        border-top: solid 1px $border-gray;
        position: absolute;
        top: 100%;
        width: 100%;
        line-height: 3;
        text-align: center;
        color: $font-gray;
    }
    &.ui-scroller-x {
        float: left;
    }
    &.out-wall {
        transition: transform 300ms cubic-bezier(0.165, 0.84, 0.44, 1) !important;
    }
}

.ui-scroller-wrap {
    position: relative;
    overflow: hidden;
}
</style>


