<template>
  <div :class="className" :style="style" @click="click">
    <slot></slot>
  </div>
</template>

<script>
export default {
  props: {
    direction: {
      type: String,
      default: 'row'
    },
    align: {
      type: String,
      default: 'stretch'
    },
    justify: {
      type: String,
      default: 'start'
    },
    inline: {
      type: Boolean,
      default: false
    },
    flex: {
      type: String,
      default: ''
    }
  },
  computed: {
    className () {
      var direction = 'T_flex_flex-flow_' + this.direction
      var align = 'T_flex_align_' + this.align
      var justify = 'T_flex_justify_' + this.justify
      var inline = this.inline ? 'T_flex_inlineflex' : 'T_flex_flex'
      return [direction, align, justify, inline]
    },
    style () {
      return 'flex: ' + this.flex
    }
  },
  methods: {
    click (e) {
      this.$emit('click', e)
    }
  }
}
</script>


<style>
.T_flex_inlineflex{
  display: inline-flex;
}
.T_flex_flex{
  display: flex;
}
.T_flex_flex-flow_row{
  flex-flow: row;
}
.T_flex_flex-flow_column{
  flex-flow: column
}
 .T_flex_align_start{
  align-items: flex-start;
}
.T_flex_align_center{
  align-items: center;
}
 .T_flex_align_end{
  align-items: flex-end;
}
 .T_flex_align_stretch{
  align-items: stretch;
}
 .T_flex_align_baseline{
  align-items: baseline;
}
.T_flex_justify_start{
  justify-content: flex-start;
}
.T_flex_justify_center{
  justify-content: center;
}
.T_flex_justify_end{
  justify-content: flex-end;
}
.T_flex_justify_between{
  justify-content: space-between;
}
.T_flex_justify_around{
  justify-content: space-around;
}
</style>



