<template>
  <div class="T_cell" @click="click" :class="{'T_cell_beclick' : beclick}">
    <div class="T_cell_body">
      <div class="T_cell_left" v-if="noLeft">
        <slot name="left">
          <div class="T_cell_icon">
            <slot name="icon"></slot>
          </div>
          <div class="T_cell_title">
            <div class="T_cell_title_h1">{{title}}</div>
            <div class="T_cell_title_h2" v-if="tips">{{tips}}</div>
          </div>
        </slot>
      </div>
      <div class="T_cell_right">
        <div class="T_cell_value">
          <slot></slot>
        </div>
        <div v-if="isLink !== undefined" class="T_cell_icon">
          <i class="iconfont icon-list_icon_path-"></i>
        </div>
        <slot name="extend"></slot>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      beclick: false
    };
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    tips: {
      type: String,
      default: ""
    },
    isLink: ""
  },
  computed: {
    noLeft() {
      return this.title || this.$slots.left || this.$slots.icon;
    }
  },
  methods: {
    click(e) {
      if (this.isLink !== undefined && !this.beclick) {
        this.beclick = true;
        setTimeout(() => {
          this.$emit("click", e);
          this.beclick = false;
        }, 100);
      }
    }
  }
};
</script>
<style>
.T_cell {
  min-height: 1.04rem;
  background-color: white;
}
.T_cell_body {
  margin-left: 0.3rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  min-height: 1.04rem;
  position: relative;
}
.T_cell_body::after {
  content: "";
  background-color: #ddd;
  height: 1px;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
  width: 100%;
  position: absolute;
  bottom: -1px;
  left: 0px;
}
.T_cell_left {
  -webkit-box-flex: 0;
  -webkit-flex: 0 1 auto;
  flex: 0 1 auto;
  margin-right: 0.32rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.T_cell_title {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
  flex-flow: column;
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
  align-items: stretch;
  -webkit-justify-content: space-around;
  justify-content: space-around;
  height: 100%;
  max-width: 5rem;
}
.T_cell_right {
  -webkit-box-flex: 1;
  -webkit-flex: 1 0 1px;
  flex: 1 0 1px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
  align-items: stretch;
  line-height: 1.04rem;
  margin-right: 0.3rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.T_cell_value {
  color: #8f8f8f;
  font-size: 0.32rem;
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
  justify-content: flex-end;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
}
.T_cell_icon {
  color: #ccc;
  margin-left: 0.24rem;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.T_cell_title_h1 {
  font-size: 0.32rem;
  color: #222;
}
.T_cell_title_h2 {
  font-size: 0.28rem;
  color: #8f8f8f;
}
.T_cell_beclick {
  background-color: #e6e6e6;
}
</style>




