<template>
  <div class="ui-cell" :class="{'ui-cell-active':active,'ui-cell-line':line,'ui-cell-bg':bg}" @click="click">
    <slot name="left"></slot>
    <slot name="right"></slot>
    <slot></slot>
  </div>
</template>
<script>
  export default {
    name: 'cell',
    props: {
      active: {
        type: Boolean, // 
        default: false
      },
      line: {
        type: Boolean, // botton-line
        default: true
      },
      bg: {
        type: Boolean, // 
        default: true
      }
    },
    methods: {
      click(e) {
        this.$emit('click', e)
      }
    }
  }
</script>
<style lang='scss'>
  .ui-cell {
    position: relative;
    align-items: flex-start;
    padding: px2rem(20) px2rem(20);
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    font-size: px2rem(28);
    margin: 0px;


    >div {
      word-wrap: break-word;
      word-break: break-all;
    }
    .center {
      text-align: center;
      width: 100%;
    }
    .gray {
      color: $font-gray
    }
  }

  .ui-cell-line {
    border-bottom: 1px solid #D9D9D9;
  }

  .ui-cell-bg {
    background: #ffffff;
  }


  .ui-cell-active:active {
    background: #eaeaea;
  }
</style>


