<template>
    <div class="btn btn-text" @click="onClick">
        <slot></slot>
    </div>
</template>

<script>
    export default {
        name: "vbtnText",
        methods: {
            onClick(e) {
                this.$emit("click", e);
            }
        }
    };
</script>



