<!--  -->
<!-- @author -->
<!-- @props placeholder -->
<!-- @props maxlength -->
<!-- @props readonly -->
<!-- @props textAlign -->
<!-- @event click -->
<!-- @event focus -->
<!-- @event blur -->
<template>
  <div class="T_input" ref="vinput" :class="[textAlign, {'T_input_disable': Tdisable}]" tabindex="1" @click="onclick" @focus="onfocus" @blur="onblur" v-if="keyBoardType === 'native'">
    <div v-if="!value && value !== 0" class="T_input_placeholder">{{placeholder}}<span class="T_input_startLight" v-if="value !== 0 && !value && !blur"></span></div>
    <div :class="{'T_input_focus': !blur && (value || value === 0)}" class="input" ref="vson" :style="'transform: translate(' + translate + 'px);'">{{type === 'password' ? value.replace(/[0-9]/g,'*') : value}}</div>
    <!-- <input ref="hiddenInput" class="hiddenInput" :disabled="disable" readonly @click="onclick" @focus="onfocus" @blur="onblur"> -->
  </div>
  <div v-else class="T_input_nature">
    <input :type="type" v-model="value" :class="[textAlign, {'T_input_disable': Tdisable}]" :placeholder="placeholder" :maxLength="maxlength" :readonly="readOnly" :disabled="Tdisable" @click="onclick" @focus="onfocus" @blur="onblur">
  </div>
</template>

<script>
export default {
  data () {
    return {
      blur: true,
      translate: 0
    }
  },
  filters: {
    passwordFilter (e) {
      if (this.type === 'password' && e) {
        return e.replace(/[0-9]/g,'*')
      } else {
        return e
      }
    }
  },
  watch: {
    value: {
      handler (e) {
        if (this.type === 'IDCard') {
          this.value = e.replace(/[^0-9X]/ig,"").replace('x', 'X')
        } else if (this.keyBoardType === 'native') {
          if (this.type === 'number') {
            this.value = e.replace(/[^0-9]/ig,"")
          }
        }
        this.$nextTick(() => {
          let fatherWidth = this.$refs.vinput && this.$refs.vinput.clientWidth
          let sonWidth = this.$refs.vson && this.$refs.vson.clientWidth
          this.translate = fatherWidth - sonWidth >= 0 ? 0 : fatherWidth - sonWidth - 2
        })
        if (e.length > this.maxlength) {
          this.value = e.substr(0, this.maxlength*1)
        } else {
          this.$emit('input', e)
        }
      },
      immediate: true
    }
  },
  props: [
    'value',
    'placeholder',
    'maxlength',
    'readonly',
    'disable',
    'type',
    'textAlign',
    'keyBoardType',
    'type'
  ],
  computed: {
    readOnly () {
      return this.readonly !== '' && this.readonly !== undefined && this.readonly !== false && this.readonly !== 'false'
    },
    Tdisable () {
      return this.disable !== '' && this.disable !== undefined && this.disable !== false && this.disable !== 'false'
    }
  },
  methods: {
    onclick (e) {
      if (!this.readOnly && !this.Tdisable) {
        this.$nextTick(() => {
          this.keyBoardType === 'native' && this.$refs.vinput.focus()
        })
        this.$emit('click', e)
      }
    },
    onfocus () {
      if (!this.readOnly && !this.Tdisable) {
        this.blur = false
        this.$emit('focus')
      }
    },
    onblur () {
      if (!this.readOnly && !this.Tdisable) {
        this.blur = true
        this.$emit('blur')
      }
    }
  }
}
</script>
<style>

.T_input_disable {
  color: #bbb !important;
}
.T_input_nature > input {
  width: 100%;
  height: 100%;
}
.T_input::-webkit-scrollbar {
  display: none;
}
.T_input {
  line-height: initial;
  overflow-x: auto;
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  color: #222;
}
.T_input > div {
    position: relative;
    display: inline-block;
    padding-right: 2px;
}
.T_input > .T_input_placeholder {
    color: #bbb;
    overflow: hidden;
    white-space: nowrap;
}
.T_input_startLight {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  width: 2px;
  background-color: #436bf1;
  height: 100%;
  display: inline-block;
  position: absolute;
  -webkit-animation: T_input_shan 1s linear infinite;
          animation: T_input_shan 1s linear infinite;
  left: 0px;
  top: 0px;
}
.T_input_focus::after {
  content: "";
  width: 2px;
  background-color: #436bf1;
  height: 100%;
  display: inline-block;
  position: absolute;
  -webkit-animation: T_input_shan 1s linear infinite;
          animation: T_input_shan 1s linear infinite;
}
@-webkit-keyframes T_input_shan {
0% {
    opacity: 0;
}
49% {
    opacity: 0;
}
50% {
    opacity: 1;
}
100% {
    opacity: 1;
}
}
@keyframes T_input_shan {
0% {
    opacity: 0;
}
49% {
    opacity: 0;
}
50% {
    opacity: 1;
}
100% {
    opacity: 1;
}
}
</style>



