<template>
  <div class="ui-search">
    <div class="set">
      <div class="set-icon fa fa-search"></div>
      <input :placeholder="placeholder" type="text" @input="search" v-model="value">
      <div v-if="value " class="clear fa fa-plus-circle" @click="clear"></div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "vSearch",
    props: {
      placeholder: {
        type: String,
        default: ''
      }
    },
    data() {
      return { value: null };
    },
    computed: {},
    watch: {},
    methods: {
      clear() {
        this.value = null
        this.$emit("input", this.value)
      },
      search() {
        this.$emit("input", this.value)
      }
    }
  }
</script>
<style lang="scss">
  .ui-search {
    -webkit-box-flex: 1;
    padding: 0 0;
    background: none;
    height: px2rem(56);
    margin: px2rem(12) 0;
    .set {
      display: -webkit-box !important;
      position: relative;
      -webkit-box-flex: 1;
      width: 100%;
      height: 100%;
      -webkit-appearance: none;
      background: none;
      text-decoration: none;
      display: block;
      background-color: #3E3E3E;
      border-radius: 10rem;
    }
    .set-icon {
      display: -webkit-box !important;
      color: #6D6D6D;
      padding: 0em 10px;
      -webkit-box-align: center;
      -webkit-box-pack: center;
    }
    input {
      display: -webkit-box !important;
      -webkit-box-flex: 1;
      padding: 0em 0em;
      font-size: 0.75em;
      color: #fff;
      border: 0px;
      background: none;
      height: 100%;
      border-radius: 0;
    }
    .clear {
      display: -webkit-box !important;
      color: #E5E8EB;
      padding: 0em 10px;
      -webkit-box-align: center;
      -webkit-box-pack: center;
      -webkit-transform: rotate(45deg);
    }
  }

  .search .search ::-webkit-input-placeholder {
    /* WebKit browsers */
    color: #6D6D6D;
  }
</style>


