<template>
    <div class="btn btn-icon" @click="onClick">
        <i v-if="!icon" class="icon iconfont icon-icon_close"></i>
        <i v-if="icon" :class="icon"></i>
    </div>
</template>

<script>
    export default {
        name: "vbtn",
        props: {
            icon: String //icon-
        },
        data() {
            return {};
        },
        methods: {
            onClick(e) {
                if (!this.icon){
	                T.Native.popWindow({
	                    data : {
	                        backPage : -1,
	                        params : {}
	                    }
	                }).then(( ret ) => {
	                    console.log( '' );
	                })
                    return false;
                }
                this.$emit("click", e);
            }
        }
    };
</script>
<style lang="scss" scoped>
.btn-icon{
    width: px2rem(80);
    height: px2rem(80);
    padding: px2rem(26);
    box-sizing: border-box;
}
</style>




