<template>
    <transition name="fade">
        <div class="toast-wrap" v-show="value">
            <p class="toast-txt">{{toastTxt}}</p>
        </div>
    </transition>
</template>

<script>
    export default {
        name: 'vToast',
        props: {
            // 
            value: {
                type: Boolean,
                default: false
            },
            // ms
            showTime: {
                type: Number,
                default: 3000
            },
            // 
            toastTxt: {
                default: ''
            }
        },
        watch: {
            value(newVal, oldVal) {
                if (newVal) {
                    let timer = setTimeout(() => {
                        this.$emit('input', false)
                        clearTimeout(timer)
                        timer = null
                    }, 3000)
                }
            }
        },
        mounted () {
            document.body.appendChild(this.$el)
        }
    }
</script>

<style lang="scss" scoped>
    .toast-wrap{
        position: fixed;
        top: 50%;
        left: 50%;
        padding: px2rem(20) px2rem(20);
        color: #ffffff;
        background-color: #000000;
        box-sizing: border-box;
        opacity: .7;
        border-radius: .1rem;
        transform: translate3d(-50%, -50%, 0);
        z-index: 999999;
        min-width: 60%;
        max-width: 90%;
        .toast-txt{
            font-size: px2rem(32);
            word-break: break-all;
            text-align: center;
        }
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to{
        opacity: 0;
    }
    .fade-enter-to, .fade-leave{
        opacity: .7;
    }
</style>



