<template>
  <div class="ui-checkbox" :class="{'ui-checked':model,'ui-disabled':disabled}">
    <i :class="{'fa fa-check':model}" @click="onChange"></i>
    <label @click="onChange">
      <div v-if="title">{{title}}</div>
    </label>
  </div>
</template>

<script>
  export default {
    name: 'vCheckbox',
    props: {
      value: Boolean, //
      disabled: Boolean, //
      label: {}, //
      title: ''
    },
    computed: {
      isGroup() { //
        let p = this.$parent;
        while (p) {
          if (p.$options.name != 'vCheckboxGroup') {
            p = p.$parent;
          } else {
            this._checkboxGroup = p;
            return true;
          }
        }
        return false;
      },
      model() {
        if (this.isGroup) {
          return this._checkboxGroup.value.indexOf(this.label) != -1;
        } else {
          return this.value;
        }
      }
    },
    methods: {
      onChange() {
        if (this.isGroup) {
          let arr = [].concat(this._checkboxGroup.value);
          if (this.model) { //
            arr.removeOf(this.label);
          } else { //
            arr.push(this.label);
          }
          this._checkboxGroup.$emit('input', arr);
        } else {
          this.$emit('input', !this.value);
        }
      }
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss"> 

  .ui-checkbox {
    position: relative;
    >i {
      position: absolute;
      top: 0;
      left: 0;
      box-sizing: border-box;
      margin-right: 0.5rem;
      width: 1.4rem;
      height: 1.4rem;
      line-height: 1.4rem;
      border: solid 1px #999;
      background-color: #fff;
      text-align: center;
      border-radius: $border-radius;
      transition: background-color 0.2s, border-color .2s;
    }
    >label {
      display: inline-block;
      //line-height: 2rem;
      padding: 0 0.5rem 0 1rem;
      //height: 2rem;
      transition: color 0.2s;
      div {
        padding: 0 0.5rem 0 1.4rem;
      }
    }
    &.ui-checked {
      &>i {
        border-color: $checkbox-checked;
        font-size: 0.9rem;
        color: #fff;
        background-color: $checkbox-checked;
      }
      &>label {
        color: $checkbox-checked;
      }
    }
    &.ui-disabled,
    .ui-disabled & {
      opacity: .6;
    }
  }
</style>


