<template>
  <div class="T_page">
    <div class="T_page_main">
      <slot class="T_page_top" name="top"></slot>
      <div class="T_page_middle" ref="middle">
        <slot></slot>
      </div>
      <slot class="T_page_bottom" name="bottom"></slot>
    </div>
    <div class="T_page_extend" :style="'height:' + options.extendHeight"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      scroll: ""
    };
  },
  props: {
    options: {
      type: Object,
      default() {
        return {
          extendHeight: "0px",
          nativeScroll: true
        };
      }
    }
  },
  watch: {
    options() {
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    }
  },
  methods: {
    scrollTo() {
      var arg = arguments;
      this.$nextTick(() => {
        this.scroll.refresh();
        this.scroll.scrollToElement(...arg);
      });
    },
    reset () {
      setTimeout(() => {
        this.scroll.finishPullDown()
        this.scroll.finishPullUp()
        this.scroll.refresh()
      }, 1500)
    }
  },
  mounted() {
    if (!this.options.nativeScroll) {
      this.scroll = new BScroll(this.$refs.middle, Object.assign({
        scrollY: true,
        click: true,
        eventPassthrough: 'horizontal'
        // bounce: false
      }, this.options.scrollOptions));
      this.scroll.on('pullingDown', function () {
        this.$emit('pullDown')
      }.bind(this))
      this.scroll.on('pullingUp', function () {
        this.$emit('pullUp')
      }.bind(this))
    }
  }
};
</script>

<style>
.T_page {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
  flex-flow: column;
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
  align-items: stretch;
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: inherit;
}
.T_page_main {
  -webkit-box-flex: 1;
  -webkit-flex: 1 0 1px;
  flex: 1 0 1px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
  flex-flow: column;
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
  align-items: stretch;
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.T_page_top {
  -webkit-box-flex: 0;
  -webkit-flex: 0 1 auto;
  flex: 0 1 auto;
}
.T_page_middle {
  -webkit-box-flex: 1;
  -webkit-flex: 1 0 1px;
  flex: 1 0 1px;
  overflow: hidden;
  position: relative;
}
.T_page_bottom {
  -webkit-box-flex: 0;
  -webkit-flex: 0 1 auto;
  flex: 0 1 auto;
}
.T_page_extend {
  -webkit-box-flex: 0;
  -webkit-flex: 0 1 auto;
  flex: 0 1 auto;
}
</style>



