/**
 * -T.Math
 *
 * format()     - 
 */

/**
 * 
 * @param {Number|String} num - 
 * @param {Boolean} [round] - 0
 * @param {Number} [precision=2] - 
 * @return {String} 
 * @example format('123456.555'); //"123,456.56"
 *          format(1.8999, true); //1.9
 *          format(1.8999, true, 2); //1.9
 *          format(1.8999, false, 3); //1.900
 */
export function format(num, round, precision = 2) {
    num = Number.parseFloat(num);
    const minus = num < 0;
    let s = Math.abs(num).toFixed(precision), //
        arr = s.split('.'), //
        trunc = arr[0], //
        decimal = arr[1], //
        len = trunc.length,
        left = len % 3, //
        ret = ''; //
    for (let i = len - 1; i >= left; i -= 3) { //3,
        ret = (i > 2 ? ',' : '') + trunc.substr(i - 2, 3) + ret;
    }
    ret = (minus ? '-' : '') + trunc.substr(0, left) + ret; //
    if (precision != 0) {
        if (round === true) { //0
            for (let i = decimal.length - 1; i >= 0; i--) { //
                if (decimal[i] != 0) {
                    ret += '.' + decimal.substr(0, i + 1); //
                    break;
                }
            }
        } else {
            ret += '.' + decimal;
        }
    }
    return ret;
}


