<template>
  <div class="ui-tabbar">
    <div v-for="(tab,i) in tabs" :key="tab.text" class="ui-item" :class="{'ui-active':i==index}" @click="onChange(i)">
      <i :class="tab.icon"></i>
      <span>{{tab.text}}</span>
    </div>
  </div>
</template>



<script>
  export default {
    name: 'vtabBar',
    props: {
      tabs: Array,
      index: {
        type: Number,
        default: 0
      }
    },
    methods: {
      onChange(index) {
        this.$emit('switch', index);
      }
    }
  }
</script>

<style lang="scss" scoped>
  $font-red:$tabBar;
  .ui-tabbar {
    display: flex;
    height: px2rem(98);
    background-color: #fff;
    .ui-item {
      flex: 1;
      text-align: center;
      font-size: 0;
      color: #333;
      i {
        display: block;
        font-size: px2rem(40);
        margin: px2rem(10) 0 px2rem(10);
      }
      span {
        font-size: px2rem(20);
      }
      &.ui-active {
        color: $font-red;
      }
    }
  }
</style>


