<template>
  <t-popup 
  :value="value"
  @onHide="hide"
  >
    <div class="T_actionSheet">
			<div class="T_actionSheet_header" v-if="title">{{title}}</div>
			<div class="T_actionSheet_warp">
        <div class="T_actionSheet_list">
          <div class="T_actionSheet_item" :class="{'T_actionSheet_choiced': selected === item.value}" v-for="(item, index) in data" :value="item.value" :key="index" @click="clickNormalItem(item)">
            <div>
              <span>{{item.key}}</span>
              <i v-if="selected === item.value" class="T_actionSheet_icon iconfont icon-list_radio"></i>
            </div>
          </div>
        </div>
        <div class="T_actionSheet_cancel" @click="nextPanel"></div>
        <slot></slot>
			</div>
		</div>
  </t-popup>
</template>
<script>
import tPopup from '../popup'
export default {
  components: {
    tPopup
  },
  data () {
    return {
      // selected: ''
    }
  },
  props: {
    value: Boolean,
    title: String,
    data: {
      type: Array,
      default () {
        return []
      }
    },
    selected: String
  },
  watch: {
    selected (e) {
      if (e) {
        this.$emit('change', e)
      }
    }
  },
  methods: {
    hide () {
      this.$emit('input', false)
    },
    clickNormalItem(item){
      if(item.value != this.selected){
        this.$emit('click', item)
        this.selected = item.value
      }
        this.$emit('input', false)
    },
  }
}
</script>
<style>
  .T_actionSheet_header {
    height: 1.04rem;
    line-height: 1.04rem;
    text-align: center;
    font-size: 0.32rem;
    background-color: white;
    color: #8f8f8f;
    min-height: 1.04rem;
  }
  .T_actionSheet_item {
    height: 1.04rem;
    line-height: 1.04rem;
    text-align: center;
    font-size: 0.32rem;
    background-color: white;
    color: #222;
    position: relative;
  }
  .T_actionSheet_item:first-child::before {
    border-top: none !important;
  }
  .T_actionSheet_item::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    top: 0px;
    left: 0px;
    border-top: 1px solid #e6e6e6;
    -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5);
  }
  .T_actionSheet_choiced {
    color: #e53935;
  }
  .T_actionSheet_cancel {
    height: 1.04rem;
    line-height: 1.04rem;
    text-align: center;
    font-size: 0.32rem;
    background-color: white;
    margin-top: 0.16rem;
    color: #222;
  }
  .T_actionSheet_icon {
    position: absolute;
    right: 0.3rem;
    color: #d81e06;
    font-size: 0.4rem;
  }
  .T_actionSheet {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-flow: column;
            flex-flow: column;
    max-height: 10rem;
    overflow: auto;
  }
  .T_actionSheet_warp {
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-flow: column;
              flex-flow: column;
  }
</style>




