/**
 * 
 */

/**
 * 
 * @return {Object} 
 *                  os - ios/android
 *                  cssPre - css-webkit-
 */
export function device() {
    return {
        os: navigator.userAgent.indexOf('iPhone') > -1 ? 'ios' : 'android', //ios/android
        cssPre: document.body.style.webkitBorderImage !== undefined ? '-webkit-' : '', //css-webkit-
        phoneModel: isIphoneX(),  //  - iphone/iphoneX/android
    };
}

// iphoneX
const isIphoneX = () => {
    let isIphone = navigator.userAgent.indexOf('iPhone') > -1 ? true : false
    if (isIphone) {
        let screenW = screen.width
        let screenH = screen.height
        let isIphoneX = 'iphone'
        if (screenH >= 812 && screenW >= 375) {
            isIphoneX = 'iphoneX'
        }
        return isIphoneX
    }
    return 'android'
}


