/**
 * T.Validator
 *
 * length           - 
 * money            - 
 * phone            - 
 * required         - ...
 * requireSelected  - ...
 */

/**
 * 
 * @param {String} v - 
 * @param {Number} min - -1
 * @param {Number} max - -1
 * @param {string} [title='{0}'] - 
 * @return {Boolean|String} true
 */
export function length(v, min, max, title = '{0}') {
    const len = v.length;
    if (min == -1) {
        if (len > max) return `${title}${max}`;
    } else if (max == -1) {
        if (len < min) return `${title}${min}`;
    } else {
        if(len > max || len < min) return `${title}${min}~${max}`;
    }
    return true;
}
/**
 * >02
 * @param {String} v - 
 * @param {string} [title='{0}'] - 
 * @return {Boolean|String} true
 */
export function money(v, title = '{0}') {
    if (isNaN(v, title = '{0}')) return title + ''; //
    if (v <= 0) return title + '0'; //0
    var str = v.toString(),
        index = str.indexOf('.');
    if (index != -1 && index < str.length - 3) return title + '2'; // && 2
    return true;
}
/**
 * 1+10
 * @param {String} v - 
 * @param {string} [title='{0}'] - 
 * @return {Boolean|String} true
 */
export function phone(v, title = '{0}') {
    if (/^1\d{10}$/.test(v)) return true;
    return title + '';
}
/**
 * ''nullundefined[]{}
 * @param {*} v - 
 * @param {string} [title='{0}'] - 
 * @return {Boolean|String} true
 * 
 * @desc required()requireSelected()
 */
export function required(v, title = '{0}') {
    if (v === 0) return true;
    if (!v || (Array.isArray(v) && !v.length) || (typeof v == 'object' && T.Object.isEmpty(v))) {
        return title + '';
    }
    return true;
}
/**
 * required()...
 */
export function requireSelected(v, title = '{0}') {
    if (required(v) !== true) {
        return '' + title;
    }
    return true;
}


