import Vue from 'vue'
import mask from './mask'
Vue.component('v-mask', mask)

// 
const MaskExtend = Vue.extend({
  template: '<v-mask :visible=visible :maskClick=maskClick @click=hide></v-mask>',
  data: function () {
    return {
      visible: false,    // mask
      maskClick: true,   // mask
      callBack: null     // 
    }
  },
  methods: {
    hide() {
      this.visible = false
      if (this.callBack) this.callBack()
    }
  }
})

let instance
export const Mask = {
  /**
   * 
   */
  show: (callBack) => {
    if (!instance) {
      instance = new MaskExtend({
        el: document.createElement('div')
      })
    }
    if (instance.visible) return
    document.body.appendChild(instance.$el)
    Vue.nextTick(() => {
      instance.visible = true
    })

    instance.callBack = callBack // 
  },
  /**
   * 
   */
  hide: () => {
    instance.hide()
  },
  /**
   * 
   */
  action: () => {

  }
}
Vue.$mask = Vue.prototype.$mask = Mask



