/**
 * 
 * T.Array      - -
 * T.Date       - -
 * T.Math       - -
 * T.Native     - 
 * T.Object     - -
 * T.message    - 
 * T.String     - -
 * T.Tracker    - 
 * T.Validator  - 
 *
 * back         - 
 * bufferTimeout    - 
 * checkFace    - 
 * distOCR      - 
 * either       - 121
 * getPath      - [,,]
 * go           - 
 * isEmpty      - ''nullundefined[]{}
 * noop         - 
 * on           - 
 * off          - 
 * px2rem       - pxrem
 * rem2px       - rempx
 * resolveUrl   - 
 * trigger      - 
 */
import * as array from './array'
import * as date from './date'
import * as http from './http'
import * as math from './math'
import * as message from './message'
import * as native from './native'
import * as object from './object'
import * as string from './string'
import * as tracker from './tracker'
import * as validator from './validator'
import {
    device
} from './_helper';

window.T = {
    Array: array,
    Date: date,
    Math: math,
    Native: native,
    Object: object,
    String: string,
    Tracker: tracker,
    message,
    Validator: validator,
    Device: device(),
    /**
     * 
     * @param {Object} [opts] - 
     * @param {object} [opts.params] - 
     * @param {string} [opts.back] - 
     *                              '#'+  - route
     *                               - 
     *                          T.go()back
     */
    back({
             params,
             back,
             options
         } = {}) {
        T.Native.popWindow({
            options: options,
            data: {
                backPage: back,
                params: params
            }
        });
    },
    _timer: {}, //bufferTimeout()timeoutcode
    /**
     * 
     * @param {String} id - 
     * @param {Function} fn - 
     * @param {Number} [millisec=500] - ms
     * @return {Number} clearTimeoutcode
     */
    bufferTimeout(id, fn, millisec = 500) {
        if (T._timer[id]) {
            clearTimeout(T._timer[id]);
        }
        T._timer[id] = setTimeout(() => {
            fn();
            delete T._timer[id];
        }, millisec);
        return T._timer[id];
    },
    /**
     * 
     * @desc T.Native.getVerificationCode()
     * @param {String} validateData - 
     * @param {Function} [loadingFun] - checkFaceloading
     * @return {Promise} - resolvereject
     *                      then
     *                      {
     *                          errorcode: 0,   //0resolve-1rejectreject
     *                          errormsg: 'ok', //
     *                      }
     */
    checkFace(validateData, loadingFun = null) {
        return new Promise((resolve, reject) => {
            T.Native.takeImage({
                videoFlag: '1'
            }).then(ret1 => {
                loadingFun && loadingFun()
                T.Native.checkFace({
                    validateData,
                    url: ret1.url
                }).then(ret2 => resolve(ret2)).catch(ret2 => reject(ret2));
            }).catch(ret1 => reject(ret1));
        });
    },
    /**
     * 
     * @param {String} frontFlag - '0'-'1'-
     * @param {Function} [loadingFun] - distOCRloading
     * @return {Promise} - resolvereject
     *                  then
     *                      {
     *                          errorcode: 0,   //0resolvereject
     *                          errormsg: 'ok', //
     *                          card_type: 0 //0-1-
     *                          ----  ----
     *                          name: '', //
     *                          sex: '',    //
     *                          nation: '',   //
     *                          birth: '1994/2/14',  //
     *                          address: '...', //
     *                          portrait: 'base64', //
     *                          id: '4406811111111111', //
     *                          frontimage: 'base64', // *
     *                          ----  ----
     *                          backimage: 'base64',  //
     *                          valid_date: '2012.07.09-2022.07.09', //
     *                          authority: '', //
     *                      }
     */
    distOCR(frontFlag, loadingFun = null) {
        return new Promise((resolve, reject) => {
            T.Native.takeImage({
                photoFlag: '0',
            }).then(ret1 => {
                loadingFun && loadingFun()
                T.Native.distOCR({
                    frontFlag,
                    url: ret1.url
                }).then(ret2 => resolve(Object.assign(ret1, ret2))).catch(ret2 => reject(ret2));
            }).catch(ret1 => {
                return reject(ret1)
            });
        });
    },
    /**
     * 121
     * @param {*} v - 
     * @param {*} option1 - 1
     * @param {*} option2 - 2
     * @return {*} 12
     * @example T.either('a', 'a', 'b'); //'b'
     */
    either(v, option1, option2) {
        return v == option1 ? option2 : option1;
    },
    /**
     * [,,]
     * @return {Array} [,,]
     */
    getPath() {
        let arr = location.pathname.split(/\/+/).slice(-3);
        arr[2] = arr[2].substr(0, arr[2].length - 5);
        return arr;
    },
    /**
     * 
     * @param {object|string} opts - string
     * @param {string} opts.path - //
     * @param {object} [opts.params={}] - 
     * @param {boolean|number|string} [opts.back=0] - 0
     *              {Number} - 0-1
     *              '#'+  - routes.jsonanchor
     *               - 
     */
    go({
           path,
           params = {},
           back = 0
       }) {
        if (typeof arguments[0] == 'string') {
            path = arguments[0];
        }
        if (path[0] !== '/') {
            path = '/' + path
        }
        T.Native.pushWindow(
            {
              data : {
                  path : path,
                  backPage : back,
                  params : params,
              },
              options : {
                  transparentTitle : false,
                  showTitleBar : false,
              }    
            },
        )
    },
    //
    goHome() {
        return T.back({
            back: 'main/mainPage/index'
        });
    },
    /**
     * ''nullundefined[]{}
     * @alias T.validator.required
     * @param {*} v - 
     * @return {Boolean} 
     */
    isEmpty(v) {
        return validator.required(v) !== true;
    },
    /**
     * 
     */
    noop() {
    },
    /**
     * 
     * @param {String} shortUrl - home/index
     * @example 'http://localhost:8090/demo/main/index.html'resolveUrl('demo/home/more'); //'http://localhost:8090/demo/home/more.html'
     */
    resolveUrl(shortUrl) {
        let pathname = string.trim(location.pathname, '/').split(/\/+/).slice(0, -3).join('/'), //location.pathname
            urlBase = location.host + (pathname ? '/' + pathname : '');
        return 'http://' + urlBase + '/' + shortUrl + '.html';
    },
    /**
     * @desc - 
     * @param  {string}   event 
     * @param  {function} callBack 
     * @param  {boolean}  useCapture 
     * @example T.on('xxxx', callBack)
     */
    on(event, callBack, useCapture = false) {
        document.addEventListener(event, e => {
            callBack(e.data || e);
        }, useCapture)
    },
    /**
     * @desc - 
     * @param  {string}   event 
     * @param  {function} callBack 
     * @param  {boolean}  useCapture 
     * @example T.off('xxxx', callBack)
     */
    off(event, callBack, useCapture = false) {
        document.removeEventListener(event, callBack, useCapture)
    },
    /**
     * pxrem
     * @param {number} px - px
     * @return {number} rem
     */
    px2rem(px) {
        let fontSize = (innerWidth * 100 / 750).toFixed(5);
        return px / fontSize;
    },
    /**
     * rempx
     * @param {number} rem - rem
     * @return {number} px
     */
    rem2px(rem) {
        let fontSize = (innerWidth * 100 / 750).toFixed(5);
        return Math.round(rem * fontSize);
    },
    /**
     * @desc - 
     * @param  {String}   event  
     * @param  {Object}   data   
     * @param  {boolean}  useCapture 
     * @param  {boolean}  cancelable 
     * @return {Event}  
     * @example T.trigger('xxxx', {})
     */
    trigger(event, data = {}, canBubble = false, cancelable = true) {
        let evt = document.createEvent('Events')
        evt.initEvent(event, canBubble, cancelable)
        evt.data = data
        document.dispatchEvent(evt)
        return evt;
    },
    /**
     * @private vm
     * @param {Vue} vm - Vue
     * @param {String} hook - 
     * @param {...*} params - 
     */
    _emitHook(vm, hook) {
        let handlers = vm.$options[hook]
        if (handlers) {
            handlers = [].concat(handlers);
            for (let i = 0, j = handlers.length; i < j; i++) {
                handlers[i].call(vm, ...[...arguments].slice(2));
            }
        }
        if (vm._hasHookEvent) {
            vm.$emit('hook:' + hook)
        }
    },
    /**
     * @private alert
     * @param {object | string} opts - stringalertmsg
     * @param {string} [opts.title] - alert
     * @param {string} opts.msg - alert
     * @param {string} [opts.cancelBtnTitle] - 
     * @param {string} [opts.confirmBtnTitle] - 
     */
    nativeAlert (opts) {
        if (typeof arguments[0] == 'string') {
            opts = {
                msg: arguments[0]
            }
        }
        return new Promise((resolve, reject) => {
            T.Native.nativeAlert(opts).then((ret) => {
                resolve(ret)
            })
        })
    },
    /*
     ** @private 
     * @param {Object} params - 
     * @param {Object} header - 
     */
    networkRequest ({tranCode, params, header = {}} = {}) {
        return new Promise((resolve, reject) => {
            T.Native.networkRequest({tranCode, params, header}).then((ret) => {
                resolve(ret)
            })
        })
    }
};


