<template>
  <div class="ui-radio" :class="{'ui-checked':model==label,'ui-disabled':disabled}">
    <i @click="onChange"></i>
    <label @click="onChange">
       <div v-if="title">{{title}}</div>
    </label>
  </div>
</template>

<script>
  export default {
    name: 'vRadio',
    props: {
      value: {}, //
      disabled: Boolean, //
      label: {}, //
      title:''
    },
    computed: {
      isGroup() { //
        let p = this.$parent;
        while (p) {
          if (p.$options.name != 'vRadioGroup') {
            p = p.$parent;
          } else {
            this._radioGroup = p;
            return true;
          }
        }
        return false;
      },
      model() {
        return this.isGroup ? this._radioGroup.value : this.value;
      }
    },
    methods: {
      onChange() {
        if (this.isGroup) {
          this._radioGroup.$emit('input', this.label);
        } else {
          this.$emit('input', this.label);
        }
      }
    },
    mounted() {
    //   let w = T.tools.rem2px(1.4),
    //     $i = this.$el.querySelector('i');
    //   w = Math.round(w / 4) * 4;
    //  $i.style.width = w + 'px';
    //   $i.style.height = w + 'px';
    }
  };
</script>

<style lang="scss" rel="stylesheet/scss">
  .ui-radio {
    position: relative;
    &>i {
      position: absolute;
      top: 50%;
      left: 0;
      box-sizing: border-box;
      margin-right: 0.5rem;
      border: solid 1px #999;
      width: 1.4rem;
      height: 1.4rem;
      transform: translateY(-50%);
      background-color: #fff;
      border-radius: 50%;
      transition: background-color 0.2s;
      &:after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 0;
        height: 0;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        background-color: #fff;
        transition: width 0.2s, height 0.2s;
      }
    }
    &>label {
      display: inline-block;
      padding: 0 0.5rem 0 1rem;
      //line-height: 2rem;
      transition: color 0.2s;
      div {
        padding: 0 0.5rem 0 1.4rem;
      }
    }
    &.ui-checked {
      &>i {
        border: none;
        background-color: $radio-checked;
        &:after {
          width: 50%;
          height: 50%;
        }
      }
      &>label {
        color: $radio-checked;
      }
    }
    &.ui-disabled,
    .ui-disabled & {
      opacity: .6;
    }
  }
</style>


