/**
 * -T.Date
 *
 * add          - 
 * format       - 
 * getDayNum    - 
 * getUTCDayCN  - 
 * isLeapYear   - 
 * string2Date  - Date
 */

/**
 * 
 * @param {Date} date - Date
 * @param {String} v - +/-y/m/d/h/i/s(/////)
 * @param {String} [formatStr] - String
 * @return {Date|String} DateformatStr
 * @example add(new Date('2012-02-29 16:04:00'), '1y'); //"2013-02-28 16:04:00"
 *          add(new Date('2017-10-18 16:04:00'), '-2m'); //"2017-08-18 16:04:00"
 */
export function add(date, v, formatStr) {
    v = v.trim();
    let num = parseFloat(v), //
        unit = v.substr(-1), //
        time = date.getTime(),
        ret;
    if (unit == 'y' || unit == 'm') { // || 
        ret = new Date(time);
        let date = date.getDate();
        ret.setDate(1); //1
        unit == 'y' ? ret.setFullYear(ret.getFullYear() + num) : ret.setMonth(ret.getMonth() + num);
        ret.setDate(Math.min(date, ret.getDayNum())); //
    } else {
        switch (unit) {
            case 'd': //
                time += num * 86400000;
                break;
            case 'h': //
                time += num * 3600000;
                break;
            case 'i': //
                time += num * 60000;
                break;
            case 's': //
                time += num * 1000;
                break;
        }
        ret = new Date(time);
    }
    return formatStr ? ret.format(formatStr) : ret;
}
/**
 * 
 * @param {Date} date - Date
 * @param {String} [formatStr='Y-m-d'] - 
 * @return {String} 
 * @example format(new Date()); //"2017-10-18 15:38:34"
 *          format(new Date(), 'Ymd N'); //"20171018 "
 */
export function format(date, formatStr = 'Y-m-d H:i:s') {
    let str = '';
    for (let c of formatStr) {
        switch (c) {
            case 'Y': //4
                str += String(date.getFullYear());
                break;
            case 'y': //2
                str += String(date.getFullYear()).substr(2);
                break;
            case 'm': //2
                str += String(date.getMonth() + 1).padStart(2, '0');
                break;
            case 'n': //0
                str += String(date.getMonth() + 1);
                break;
            case 'd': //2
                str += String(date.getDate()).padStart(2, '0');
                break;
            case 'j': //0
                str += String(date.getDate());
                break;
            case 'H': //4
                str += String(date.getHours()).padStart(2, '0');
                break;
            case 'i': //2
                str += String(date.getMinutes()).padStart(2, '0');
                break;
            case 's': //2
                str += String(date.getSeconds()).padStart(2, '0');
                break;
            case 'N': //1
                str += dategetUTCDayCN();
                break;
            default:
                str += c;
        }
    }
    return str;
}
/**
 * YYYY-MM-DDYYYY-MM-DD HH:II:SSHH:II:SS
 * @param {String} v - 
 * @return {String} 
 */
export function formatDateDtl(v) {
    v = v.toString();
    let len = v.length;
    if (len == 14 || len == 12) { //YYYY-MM-DD HH:II:SS || YYYY-MM-DD HH:II
        return `${v.substr(0, 4)}-${v.substr(4, 2)}-${v.substr(6, 2)} ${v.substr(8, 2)}:${v.substr(10, 2)}${len == 14 ? ":" + v.substr(12, 2) : ""}`;
    } else if (len == 8) { //YYYY-MM-DD
        return `${v.substr(0, 4)}-${v.substr(4, 2)}-${v.substr(6, 2)}`;
    } else if (len == 6 || len == 4) { //HH:II:SS || HH:II
        return `${v.substr(0, 2)}-${v.substr(2, 2)}-${len == 6 ? ":" + v.substr(4, 2) : ""}`;
    }
    return v;
}
/**
 * 
 * @param {Date} date - Date
 * @return {Number} 
 */
export function getDayNum(date) {
    const m = date.getMonth() + 1;
    if (m == 2) { //2
        return isLeapYear() ? 29 : 28;
    } else if (m == 4 || m == 6 || m == 9 || m == 11) {
        return 30;
    } else {
        return 31;
    }
}
/**
 * 
 * @param {Date} date - Date
 * @returns (String)...
 */
export function getUTCDayCN(date) {
    switch (date.getUTCDay()) { //0()6()
        case 0:
            return '';
        case 1:
            return '';
        case 2:
            return '';
        case 3:
            return '';
        case 4:
            return '';
        case 5:
            return '';
        case 6:
            return '';
    }
}
/**
 * 
 * @param {Date} date - Date
 * @return {Boolean} true false
 */
export function isLeapYear(date) {
    const year = date.getFullYear();
    return (year % 4 == 0 && year % 100 != 0) || (year % 400 == 0);
}
/**
 * Date
 * @param {String} v - YYYY-MM-DDYYYY-MM-DD HH:II:SSYYYYMMDDYYYYMMDDHHIISS
 * @return {Date|Null} vDatenull
 */
export function string2Date(v) {
    let str = dateformatDateDtl(v);
    if (str.length < 10) {
        //
        return null;
    }
    str = str.replace(/\-/g, '/');
    if (str.length < 19) {
        //
        str += " 00:00:00".substr(str.length - 19);
    }
    return new Date(str);
}


