<template>
    <div class="ui-alert" :class="{'ui-confirm':confirm}" v-if="value">
        <div>
            <h3 v-if="title">{{title}}</h3>
            <div class="content">
                <slot></slot>
            </div>
            <v-input v-if="typeof inputValue == 'string'" :type="inputType" :maxlength="inputMaxlength" v-model="myInputValue"/>
            <div class="btns">
                <v-button v-for="(item,i) in btns" :key="i" :class="{focus:!confirm || i==focus}" @click="onclick(i, $event)">
                    {{item.text}}
                </v-button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'vAlert',
    props: {
        value: Boolean, //
        confirm: Boolean, //
        title: String, //
        buttonText: [String, Array], //
        closable: { //
            type: Boolean,
            default: true
        },
        focus: { //
            type: Number,
            default: 1
        },
        callback: [Function, Array], //
        inputValue: String, //
        inputType: { //
            type: String,
            default: 'text'
        },
        inputMaxlength: Number //
    },
    data() {
        let btns,
            value;
        if (this.confirm) { //
            btns = [{
                text: ''
            }, {
                text: ''
            }];
            if (this.buttonText) {
                if (typeof this.buttonText == 'string') {
                    btns[1].text = this.buttonText;
                } else {
                    btns[0].text = this.buttonText[0];
                    btns[1].text = this.buttonText[1];
                }
            }
            if (this.callback) {
                if (typeof this.callback == 'function') {
                    btns[1].callback = this.callback;
                } else {
                    btns[0].callback = this.callback[0];
                    btns[1].callback = this.callback[1];
                }
            }
        } else {
            btns = [{
                text: this.buttonText || '',
                callback: this.callback
            }];
        }
        return {
            btns,
            myInputValue: this.inputValue
        }
    },
    methods: {
        onclick(index, e) { //
            if (this.btns[index].callback) {
                let args = [];
                if (typeof this.myInputValue == 'string') {
                    args.push(this.myInputValue);
                }
                args.push(e);
                if (this.btns[index].callback(...args) === false) return;
            }
            this.close(e);
        },
        close(e) { //
            this.$emit('input', false);
            this.$emit('closed', e);
        }
    },
    mounted(){
        document.body.appendChild(this.$el)
    }
}
</script>

<style lang="scss">
.ui-alert {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5000;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 10005;
    & > div {
        position: relative;
        width: 75%;
        overflow: visible;
        text-align: center;
        border-radius: t(20);
        background-color: #fff;
        h3 {
            padding: 0.5rem 1rem;
            font-size: 1.1rem;
            font-weight: bold;
            text-align: center;
            border-bottom: solid 1px #bbb;
            background-color: #fff;
            border-radius: t(20) t(20) 0 0;
            & ~ .content {
                border-radius: 0;
            }
        }
        .content {
            padding: t(45);
            min-height: t(60);
            background-color: #fff;
            border-radius: t(20) t(20) 0 0;
        }
        .ui-input {
            margin: -0.5rem 1rem 1rem;
            input {
                padding: 0.2rem 0.5rem;
                height: auto;
            }
            .ui-input-cross {
                margin: 0;
                padding: 0.2rem 0.5rem;
            }
        }
        .btns {
            display: -webkit-box;
            overflow: hidden;
            border-radius: 0 0 t(20) t(20);
            border-top: solid 1px #ddd;
            button {
                -webkit-box-flex: 1;
                margin: 0;
                padding: t(30);
                border-radius: 0;
                @include button-color(#fff, #666);
                &.focus {
                    @include button-color(#fff, #e73636);
                }
                &:nth-child(2) {
                    border-left: solid 1px #ddd;
                }
            }
        }
    }
    &.no-bg {
        background: none;
    }
}
</style>


